<template>
  <side-bar-page>
    <template slot="content">
      <!-- <button @click="test">Test</button> -->
      <b-input-group size="sm" v-if="isMobileResolution">
        <b-input-group-prepend>
          <b-btn variant="secondary" @click="onSubmit">{{$t('search')}}</b-btn>
        </b-input-group-prepend>
        <b-form-input @keydown.native="onKeyPress" v-model="searchText"></b-form-input>
      </b-input-group>
      <div v-if="isSearching">
        {{$t('loading')}}...
        <spinner-loader color="red"></spinner-loader>
      </div>
      <div v-if="!isSearching && !(query && query.length)" style="text-align: center;">{{$t('noFind')}}...</div>
     <div v-if="!isSearching && query && query.length && !users.length" style="text-align: center;">{{$t('noFind')}}...</div>
      <div v-if="!isSearching && query && query.length && users.length" class="container-fluid" style="padding-top: 10px; margin-left: 10px;">
        <router-link v-for="user in users" :key="user.id" :id="user.id" :to="{name: 'profile', params: {id: user.id}}">
          <div style="width: 100%;">
            <img :src="user.avatar" width="50" height="50"/>
            {{user.firstName}} {{user.lastName}}
          </div>
        </router-link>
      </div>
    </template>
  </side-bar-page>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';
  import SideBarPage from '../components/SideBarPage'
  import {USER_SEARCH,USER_AVATAR_LOAD} from "../store/actions/userConst";
  import SpinnerLoader from 'vue-spinner/src/PulseLoader.vue'

  export default {
    name: "Search",
    props: {
      query: '',
    },
    data() {
      return {
        users: [],
        cacheAvatars: [],
        searchText: '',
      }
    },
    created() {
      this.searchText = this.query
      this.startSearch()
    },
    watch: {
      '$route'() {
        // обрабатываем изменение параметров маршрута...
        this.startSearch(); // Запуск отрисовки
      }
    },
    computed: {
      ...mapGetters(['isMobileResolution', 'isSearching'])
    },
    methods: {
      ...mapActions([USER_SEARCH,USER_AVATAR_LOAD]),
      test() {
        console.log(this.users)
      },
      onSubmit() {
        // this.$router.push({name: 'home'})
        this.$router.push({name: 'search', query: { q: this.searchText } })
      },
      onKeyPress (evt) {
        if(evt.code==='Enter' || evt.code==='NumpadEnter')
          this.onSubmit()
      },
      startSearch() {
        if(this.query && this.query.length) {
          this.USER_SEARCH(this.query)
            .then((res)=>{
              this.users = res
              this.users.forEach(u => {
                if(!~this.cacheAvatars.findIndex(c=>c.id===u.id)) {
                  this.cacheAvatars.push({id: u.id})
                }
              })
              this.cacheAvatars.forEach(c => {
                if(!c.avatar) {
                  this.USER_AVATAR_LOAD(c)
                    .then((img)=>{
                      c.avatar = img
                      this.users.filter(u=>u.id===c.id).forEach(u1=>u1.avatar=c.avatar)
                      this.users.push({})
                      this.users.splice(this.users.length-1,1)
                    })
                    .catch((err)=>console.log(err))
                } else {
                  this.users.filter(u=>u.id===c.id).forEach(u1=>u1.avatar=c.avatar)
                  this.users.push({})
                  this.users.splice(this.users.length-1,1)
                }
              })
            })
            .catch((err)=>console.log(err))
        }
      },
    },
    components: { SideBarPage, SpinnerLoader },
  }
</script>

<style lang="sass" scoped>

</style>
