<template>
  <div class="flex-container" :class="{'reverse-flex': isMobileResolution}">
    <div class="desktop-sidebar bImage" v-if="!isMobileResolution">
      <div class="redBackground">
        <div v-if="!getProfileImg" class="logo-image"><img src="../assets/img/new_logo.png" @click="home(true)"/></div>
        <div v-if="getProfileImg" class="logo-image"><img class="image-circle" :src="getProfileImg" @click="home(true)"/></div>
        <side-bar/>
        <div class="row">
          <div class="col-md-12">
            <div class="social-links">
              <div class="social-label">
                <span>{{$t('s_community')}}</span>
              </div>
              <a href="https://twitter.com/universtage?lang=en" target="_blank">
                <span class="fa-stack fa-lg twitter">
                  <i class="fa fa-twitter fa-stack-1x"></i>
                </span>
              </a>
              <a href="https://www.facebook.com/universtage" target="_blank">
                <span class="fa-stack fa-lg facebook">
                  <i class="fa fa-facebook fa-stack-1x"></i>
                </span>
              </a>
              <a href="https://vk.com/universtage" target="_blank">
                <span class="fa-stack fa-lg vkontakte">
                  <i class="fa fa-vk fa-stack-1x"></i>
                </span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="mobile-sidebar bImage" v-if="isMobileResolution" :class="{'hide-mobile-menu': hideMobileMenu}">
      <div class="redBackground">
        <div v-if="!getProfileImg" class="logo-image"><img src="../assets/img/new_logo.png" @click="home(true)"/></div>
        <div v-if="getProfileImg" class="logo-image"><img class="image-circle" :src="getProfileImg" @click="home(true)"/></div>
        <top-nav class="navbar-m"/>
        <side-bar/>
        <div class="row">
          <div class="col-md-12">
            <div class="social-links">
              <div class="social-label">
                <span>{{$t('s_community')}}</span>
              </div>
              <a href="https://twitter.com/universtage?lang=en" target="_blank">
                <span class="fa-stack fa-lg twitter">
                  <i class="fa fa-twitter fa-stack-1x"></i>
                </span>
              </a>
              <a href="https://www.facebook.com/universtage" target="_blank">
                <span class="fa-stack fa-lg facebook">
                  <i class="fa fa-facebook fa-stack-1x"></i>
                </span>
              </a>
              <a href="https://vk.com/universtage" target="_blank">
                <span class="fa-stack fa-lg vkontakte">
                  <i class="fa fa-vk fa-stack-1x"></i>
                </span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="scroll" class="content content-base" :class="{'move-content': isMobileResolution && !hideMobileMenu}">
      <div class="container-fluid" v-if="isMobileResolution">
        <div class="row">
          <div class="col-md-12">
            <button type="button" aria-label="Close" class="close" @click="hideMobileMenu=!hideMobileMenu"><i class="fa fa-bars" aria-hidden="true"></i></button>
          </div>
        </div>
      </div>
      <top-nav v-if="!isMobileResolution"/>
      <slot name="content"></slot>
    </div>
  </div>
</template>

<script>
  import SideBar from './SideBar';
  import TopNav from './TopNavBar';
  import { mapGetters } from 'vuex';

  export default {
    data() {
      return {
        hideMobileMenu: true
      }
    },
    computed: {
      ...mapGetters(['windowWidth', 'mobileResolution', 'getProfileImg']),
      isMobileResolution() {
        return this.windowWidth<=this.mobileResolution;
      }
    },
    methods: {
      home(profile) {
        if(profile) {
          this.$router.push({name: 'updateProfile'})
        } else {
          this.$router.push({name: 'home'})
        }
      },
    },
    components: { SideBar, TopNav }
  }
</script>

<style lang="scss" scoped>
  $sidebar-width: 260px;

  .flex-container {
    display: -webkit-flex;
    display: flex; /*отображает контейнер как блочный элемент*/
    height: 100vh;
  }
  .desktop-sidebar {
    height: 100%;
    flex: 0 0 $sidebar-width;
    z-index: 999 !important;
    /*background: linear-gradient(to bottom, #FB404B 0%, rgba(187, 5, 2, 0.7) 100%);*/
  }
  .content {
    flex: 1;
    width: 100%;
    transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
    padding: 10px;
    overflow-y: auto;
    background-color: #F7F7F8 !important;
    padding: 0 !important;
  }
  .content-base {
    margin-top: 57px;
  }
  .mobile-sidebar {
    flex: 0 0 $sidebar-width;
    height: 100%;
    position: fixed;
    width: $sidebar-width;
    transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
    /*background: linear-gradient(to bottom, #FB404B 0%, rgba(187, 5, 2, 0.7) 100%);*/
  }
  .move-content {
    transform: translate3d(-$sidebar-width, 0, 0);
  }
  .hide-mobile-menu {
    transform: translate3d($sidebar-width, 0, 0);
  }
  .reverse-flex {
    flex-direction: row-reverse;
  }
  .bImage {
    background: url(../assets/img/full-screen-image-1.jpg);
    background-size: cover;
    background-position: center center;
    height: 100vh;
  }
  .redBackground {
    padding-top: 10px;
    height: 100%;
    background: linear-gradient(to bottom, #FB404B 0%, rgba(187, 5, 2, 0.7) 100%);
  }
  .logo-image {
    text-align: center;
    height: 110px;
    width: 100%;
    img.image-circle{
      text-align: center;
      height: 100%;
      cursor: pointer;
      border: 2px solid #eee;
      border-color: #eee;
      border-radius: 100px;
      background-color: white;
    }
  }
  .social-links {
    position: fixed;
    bottom: 15px;
    text-shadow: 1px 1px 1px rgba(76, 76, 76, 0.8);
  }
  .social-links .social-label {
    color: rgba(255,255,255,.9) !important;
    margin-left: 30px;
    padding-bottom: 10px;
  }
  .social-links i {
    color: rgba(255,255,255,.9) !important;
    cursor: pointer;
    transition: all 0.1s;
  }
  .social-links i:hover {
    transform: scale(1.3);
  }
  .social-links span.twitter {
    margin-left: 55px;
  }
  .social-links > span.fa-lg {
    font-size: 23px;
  }
  @media only screen and (max-width: 768px) {
    .content-base {
      margin-top: 0 !important;
    }
    .close {
      margin-top: 10px;
      margin-bottom: 10px;
    }
    .navbar-m {
      background-color: transparent !important;
      text-align: center !important; 
    }
    .navbar-m a.nav-link {
      color: rgba(255,255,255,.9) !important;
      text-shadow: 1px 1px 1px rgba(76, 76, 76, 0.8);
    }
    .social-links {
      position: relative;
      margin-top: 30px;
    }
  }
</style>

<style>
@media (max-width: 768px) {
  .navbar-m .dropdown-toggle::after {
    color: rgba(255,255,255,.9) !important;
    text-shadow: 1px 1px 1px rgba(76, 76, 76, 0.8);
  }
}
</style>
