<template>
  <b-navbar toggleable="md">
    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
    <b-navbar-brand href="#">
      <img src="../assets/img/new_logo.png" :class="$style.logo"/>
    </b-navbar-brand>

    <b-collapse is-nav id="nav_collapse">

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto" :class="$style.myClass">
        <b-nav-item v-if="isLogin" to="" @click="logOut"><i class="fa fa-sign-out" aria-hidden="true"></i> {{$t('logOut')}}</b-nav-item>
        <template v-else>
          <b-nav-item :to="{name: 'login'}"><i class="fa fa-sign-in" aria-hidden="true"></i> {{$t('logIn')}}</b-nav-item>
          <b-nav-item :to="{name: 'register'}"><i class="fa fa-user-plus" aria-hidden="true"></i> {{$t('register')}}</b-nav-item>
        </template>
        <b-nav-item-dropdown left>
          <template slot="button-content">
            <i class="fa fa-globe"></i><span> {{$t('language')}}</span>
          </template>
          <b-dropdown-item v-for="lng in languages" href="#" @click="setLang(lng[0])" :key="lng[0]">
            <img :src="`/img/flags/${getImgName(lng[0].toUpperCase())}.png`" style="padding-right: 10px"/>{{lng[1]}}
            <!-- {{lng[1]}} -->
          </b-dropdown-item>
          
          <!-- <b-dropdown-item v-for="lng in languages" href="#" @click="setLang(lng[0])" :key="lng[0]">
            <img :src="`/img/flags/${getImgName(lng[0].toUpperCase())}.png`" style="padding-right: 10px"/>{{lng[1]}}
          </b-dropdown-item>           -->
       
          <!--<b-dropdown-item href="#" @click="setLang('ru')">Русский</b-dropdown-item>-->
          <!--<b-dropdown-item href="#" @click="setLang('fr')">Français</b-dropdown-item>-->
          <!--<b-dropdown-item href="#" @click="$store.commit('LOGIN_UPDATED', !isLogin)">{{buttonsText[isLogin?1:0]}}</b-dropdown-item>-->
          <!--<b-dropdown-item href="#" @click="$store.dispatch('loginUpdate', !isLogin)">{{buttonsText[isLogin?1:0]}}</b-dropdown-item>-->
        </b-nav-item-dropdown>
      </b-navbar-nav>

    </b-collapse>
  </b-navbar>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { LANG_SETLNG } from '../store/actions/langConst'
  import { AUTH_LOGOUT } from '../store/actions/userConst'

  export default {
    data () {
      return {
        buttonsText: ['Log In', 'Log Out']
      }
    },
    computed: {
      ...mapGetters(['isLogin', 'languages'])
    }
    ,methods: {
      getImgName(lng) {
        return lng == 'EN'?'GB':lng;
      },
      setLang(lang) {
        this.$store.dispatch(LANG_SETLNG, lang);
      },
      logOut() {
        this.$store.dispatch(AUTH_LOGOUT).then(() => {
          this.$router.push({name: 'home'});
        });
      }
    }
  }
</script>

<style lang="scss" module>
  .myClass {
    li>a {
      color: rgba(255,255,255,.7) !important;
      i {
        color: rgba(255,255,255,.7) !important;
      }
      &:hover {
        color: rgba(255,255,255,1) !important;
        i {
          color: rgba(255,255,255,1) !important;
        }
      }
    }
  }

  .logo{
       display: inline-block;
  }

  @media (max-width: 768px) {
    .logo{
       width: 20%;
       position: absolute;
       left: 40%;
       top: 5px;
    }
    nav{
      z-index: 999999;
      background-color: #FB404B;
    }
  }
</style>