<template>
  <side-bar-page>
    <template slot="content">
      <!-- <button @click="test">Test</button> -->
      <div class="container-fluid">
        <div v-if="isLoadingJobAds" :class="$style.stopUserEvents"></div>
        <div class="row">
          <div class="col-md-3"></div>
          <div class="col-md-6">
            <div class="jobs-box">
                <h3 :class="$style.header">{{$t('jobAds')}}</h3>
                <br>
                <h5 :class="$style.header2">{{$t('text11')}}</h5>
              <b-row>
                <b-col style="min-width: 180px;">
                 <label>{{$t('artistType').toUpperCase()}}*</label>
                  <select class="form-control" v-model="selectArtistType" @change="changeSelect('type')">
                    <option :disabled="selectArtistType" :value="null">- {{$t('type')}} -</option>
                    <option v-for="type in getTypes" :value="type" :key="type.type_en">
                      {{ type[`type_${currentLng}`] }}
                    </option>
                  </select>
                </b-col>
                <b-col style="min-width: 180px;">
                  <label>{{$t('categories').toUpperCase()}}</label>
                  <select class="form-control" v-model="selectArtistCategories" @change="changeSelect()">
                    <option :value="null">- {{$t('category')}} -</option>
                    <option v-for="cat in getGategories" :value="cat" :key="cat.name_en">
                      {{ cat[`name_${currentLng}`] }}
                    </option>
                  </select>
                </b-col>
                <b-col style="min-width: 180px;">
                  <label>{{$t('skillsAndStyles').toUpperCase()}}</label>
                  <select class="form-control" v-model="selectArtistSkill">
                    <option :value="null">- {{$t('skillOrStyle')}} -</option>
                    <option v-for="skill in getSkills" :value="skill" :key="skill.skill_en">
                      {{ skill[`skill_${currentLng}`] }}
                    </option>
                  </select>
                </b-col>
              </b-row>
              <br>
              <b-row>
                <b-col>
                  <circle-button :iAmArtist="true" :selectButton="isLookingJob" @click="isLookingJob=$event"/>
                </b-col>
                <b-col>
                  <circle-button :iAmArtist="false" :selectButton="!isLookingJob" @click="isLookingJob=$event"/>
                </b-col>
              </b-row>
              <b-row v-if="!isLoadingJobAds">
                <b-col cols="12" style="text-align: center; margin-bottom: 5px">
                  <button class="btn btn-fill btn-danger btn-round btn-sm btn-wd" :class="{[$style.buttonDisabled]: !selectArtistType}" @click="callJobAdsApi" :disabled="!selectArtistType">{{$t('showResult')}}</button>
                </b-col>
                <b-col cols="12" style="text-align: center;">
                  <!-- <button class="btn btn-fill btn-danger btn-round btn-sm btn-wd" :class="{[$style.buttonDisabled]: !selectArtistType}" @click="callJobAdsApi({email: getUserProfile.email, firstName: getUserProfile.firstName})" :disabled="!selectArtistType">{{$t('sendByEmail')}}</button> -->
                  <b-form-checkbox plain v-model="sendEmail" :disabled="realEmail ? false : true" :title="!realEmail ? $t('hintEmail') : ''">
                    <strike v-if="!realEmail">{{$t('sendEmail')}}</strike>
                    <template v-if="realEmail">{{$t('sendEmail')}}</template>
                  </b-form-checkbox>
                </b-col>
              </b-row>
              <b-row v-if="isLoadingJobAds" style="justify-content: center">
                <sync-loader class="text-center" color="red"></sync-loader>
              </b-row>
            </div>
          </div>
          <div class="col-md-3"></div>
        </div>
        <span id="element"></span>
        <div v-if="!jobAdsList.length && sendRequest" style="text-align: center;">{{$t('noFind')}}...</div>
        <div v-if="jobAdsList.length" style="padding: 20px;">
          <b-row v-for="post in jobAdsList" :key="post.postLink">
            <hr>
            <p :class="$style.postText">{{post.message}}</p>
            <p :class="$style.postLabel">Для более полной информации, пожалуйста перейдите по ссылке:</p>
            <a :href="post.postLink" target="_blank">{{post.postLink}}</a>
          </b-row>
        </div>
      </div>
    </template>
  </side-bar-page>
</template>

<script>
  import CircleButton from '../components/CircleButton'
  import SideBarPage from '../components/SideBarPage'
  import { mapActions, mapGetters } from 'vuex';
  import {GET_ARTIST_TYPES, GET_JOB_ADS, GET_SKILLS} from "../store/actions/skillsConst";
  import syncLoader from 'vue-spinner/src/PulseLoader.vue'

  export default {
    created() {
      this.GET_ARTIST_TYPES()
        .then(res=>this.artistTypes=res)
        .catch(err=>console.log(err))
      this.GET_SKILLS()
        .then(res=>this.skills=res)
        .catch(err=>console.log(err))
    },
    data() {
      return {
        isLookingJob: true,
        skills: [],
        artistTypes: [],
        selectArtistType: null,
        selectArtistCategories: null,
        selectArtistSkill: null,
        jobAdsList: [],
        sendEmail: false,
        sendRequest: false,
      }
    },
    computed: {
      ...mapGetters(['currentLng','isLoadingJobAds','getUserProfile','realEmail']),
      getSkills() {
        return this.selectArtistCategories ? this.skills.filter(skill=>this.selectArtistCategories.skills.indexOf(skill.code)!=-1)
          .sort((a,b)=>a[`skill_${this.currentLng}`].localeCompare(b[`skill_${this.currentLng}`])): null
      },
      getGategories() {
        let data = JSON.parse(JSON.stringify(this.selectArtistType))
        return this.selectArtistType ? data.categories.sort((a,b)=>a[`name_${this.currentLng}`].localeCompare(b[`name_${this.currentLng}`])) : null
      },
      getTypes() {
        let data = JSON.parse(JSON.stringify(this.artistTypes))
        return data.sort((a,b)=>a[`type_${this.currentLng}`].localeCompare(b[`type_${this.currentLng}`]))
      },
    },
    methods: {
      ...mapActions([GET_SKILLS, GET_ARTIST_TYPES, GET_JOB_ADS]),
      test() {
        console.log(this.realEmail);
      },
      changeSelect(type) {
        if(type) {
          this.selectArtistCategories = null
          this.selectArtistSkill = null
        } else {
          this.selectArtistSkill = null
        }
      },
      callJobAdsApi() {
        let data = {
          email: this.sendEmail ? this.realEmail : null,
          firstName: this.sendEmail ? this.getUserProfile.firstName : null,
          skill: this.selectArtistSkill ? this.selectArtistSkill.skill_en : null,
          performerTypeName: this.selectArtistType ? this.selectArtistType.type_en: null,
          participantType: this.isLookingJob ? 1 : 2,
          languageId: this.currentLng,
          category: this.selectArtistCategories ? this.selectArtistCategories.name_en : null,
        }
        for(let key in data) {
          if(!data[key]) delete data[key]
        }
        this.GET_JOB_ADS(data)
          .then(res=>{
            this.jobAdsList=res
            this.$scrollTo('#element', 1000, { container: '#scroll' })
            this.sendRequest = true
          })
          .catch(err=>console.log(err))
      },
    },
    components: { SideBarPage, syncLoader, CircleButton },
  }
</script>

