import { LANG_SETLNG } from '../actions/langConst'
import {app} from '../../main'

const state = {
    // languages: [['en', 'English'], ['ru', 'Русский'], ['fr', 'Français']],
    languages: [['en', 'English'], ['ru', 'Русский']],
  currentLng: localStorage.getItem('ustage-language') || 'en'
}

const getters = {
  languages: state => state.languages,
  currentLng: state => state.currentLng
}

const actions = {
  [LANG_SETLNG]: ({commit }, payload) => {
    commit(LANG_SETLNG, payload)
  }
}

const mutations = {
  [LANG_SETLNG]: (state, payload) => {
    app.$i18n.locale = payload
    state.currentLng = payload;
    localStorage.setItem('ustage-language', payload);
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
}
