                              </p>
                              <i class="fa fa-angle-down chevron-collapse"></i>
                            </b-btn>
                          </b-card-header>
                          <b-collapse id="accordion1" accordion="my-accordion" role="tabpanel">
                            <b-card-body class="b-1">
                               <div class="row">
                                <div class="col-md-12" :hidden="cropCover.loading">
                                  <croppa 
                                    v-model="cropCover"
                                    :width="1920"
                                    :height="400"
