                        </b-card>
                        <b-card no-body class="mb-1">
                          <b-card-header header-tag="header" class="p-1" role="tab">
                            <b-btn block href="#" v-b-toggle.accordion2 variant="default">
                              <p class="p-title title-collapse">
                                <span class="step-label">2.</span> 
                                <span class="step-text">{{$t('pr_customize_step2')}}</span>
                              </p>
                              <i class="fa fa-angle-down chevron-collapse"></i>
