                                    canvas-color="white"
                                    :show-remove-button="false"
                                    :show-loading="false"
                                    accept="image/*"
                                    :file-size-limit="35 * 1024 * 1024"
                                    @file-type-mismatch="onFileTypeMismatch"
                                    @file-size-exceed="onFileSizeExceed"
                                    :zoom-speed="10"
                                    prevent-white-space
                                    @init="onInitCropImage"
                                    initial-size="cover"
                                    initial-position="center"
                                  >
                                  <div class="spinner" v-if="cropPhoto && cropPhoto.loading"></div>
                                  </croppa>
                                  <div class="text-center">  
                                    <i class="fa fa-plus crop-btn" @click="cropPhoto.zoomIn()"></i>
                                    <i class="fa fa-minus crop-btn" @click="cropPhoto.zoomOut()"></i>
                                  </div>
                                  <div class="text-center">
                                    <button class="btn btn-default select-btn" @click="cropPhoto.chooseFile()">
                                    <i class="fa fa-folder-open-o"></i> 
                                    {{$t('pr_customize_select')}}</button>
                                    <button class="btn btn-default select-btn" @click="cropPhoto.remove()">
                                    <i class="fa fa-trash-o" aria-hidden="true"></i>
                                    {{$t('pr_customize_remove')}}</button>
                                    <p class="select-text">{{$t('pr_customize_fileSize')}}</p>
                                  </div>
