<script>
  import SideBarPage from '../components/SideBarPage';
  import { mapActions, mapGetters} from 'vuex';
  import { USER_LOAD, USER_SEND, USER_COVER_SEND, USER_AVATAR_SEND, USER_REQUEST, USER_DELETE, AUTH_LOGOUT } from "../store/actions/userConst";
  import selectSkills from "../components/SelectSkills";
  import syncLoader from 'vue-spinner/src/PulseLoader.vue';
  import swal from 'sweetalert2';
  import datepicker from 'vuejs-datepicker';
  import {en, ru} from 'vuejs-datepicker/dist/locale';
  var moment = require('moment');
  moment().format();

  export default {
    components: { SideBarPage, syncLoader, selectSkills, datepicker },
    created() {
      this.USER_LOAD()
            .then(res=> {
                this.user = res;
                console.log('USER_LOAD');
                console.log(res);
                this.showAgreementModal = !this.user.isAgreementAccepted;
                this.getNickName();
              })
            .catch(err=>console.log(err));
    },
    data(){
      return {
        countriesList: [],
        skills: [],
        artistTypes: [],
        selectArtistType: null,
        selectArtistCategories: null,
        selectArtistSkill: null,
        cropPhoto: {},
        cropCover: {},
        showImageProfile: false,
        showImageCover: false,
        showAgreementModal: false,
        blobImageProfile: Object,
        blobImageCover: Object,
        coverFiles: this.$t('pr_no_file'),
        regexEmail: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
        isChangedProfile: false,
        user: {
            firstName: '',
            lastName: '',
            city: '',
            country: '',
            phone1: '',
            email: '',
            nickName: '',
            teamName: '',
            numberOfPersons: '',
            aboutMe: '',
            artistTypesList: [],
            userType: 'artist',
        },
      }
    },
    computed: {
      ...mapGetters(['currentLng', 'getUserProfile', 'getProfileImg', 'getCoverImg', 'isUserSending', 'isUploadingCover', 'isUploadingAvatar']),
      cropImagePlaceholder() {
        return this.cropPhoto && this.cropPhoto.loading ? '' : this.$t('pr_drop_file')
      }
    },
    methods: {
      ...mapActions([USER_LOAD, USER_SEND, USER_COVER_SEND, USER_AVATAR_SEND, USER_DELETE, AUTH_LOGOUT]),
      calendarLng() {
        if (this.currentLng === 'ru') {
          return ru;
        } else if (this.currentLng === 'en') {
          return en;
        }
      },
      dateFormatter(date) {
        return moment(date).format('MM-DD-YYYY');
      },
      setPlace(place) {
        this.user.city = place.address_components[0].long_name;
        this.user.country = place.address_components[place.address_components.length - 1].long_name;
        console.log(place);
      },
      setCountry(place) {
        this.user.country = place;
      },
      uploadCroppedPhoto(compressionRate) {
        this.cropPhoto.generateBlob((blob) => {
          blob.lastModified = new Date();
          blob.name = 'my-image.png';
          blob.__proto__ = File.prototype;
          this.blobImageProfile = blob;
          this.postProfileImage(blob.name, blob);
        }, 'image/png', compressionRate);
      },
      uploadCroppedCover(compressionRate) {
        this.cropCover.generateBlob((blob) => {
          blob.lastModified = new Date();
          blob.name = 'my-image.jpg';
          blob.__proto__ = File.prototype;
          this.blobImageCover = blob;
          this.postCoverImage(blob.name, blob);
        }, 'image/jpeg', compressionRate);
      },
      postCoverImage(name, blob) {
        let formData = new FormData();
        formData.append('file', blob, name);
        this.USER_COVER_SEND({
          image: formData,
          isProfile: false
        }).then((res)=>{
            console.log(res);
            this.$store.dispatch(USER_REQUEST)
              .then((resp)=>console.log(resp))
              .catch((err)=>console.log(err));
            const Toast = swal.mixin({
              toast: true,
              position: 'bottom-start',
              customClass: 'sweet-modal',
              showConfirmButton: false,
              timer: 3000
            });
            Toast.fire({
              title: '<i class="fa fa-check fa-2x"></i>' + this.$t('pr_cover_uploaded')
            });
        })
        .catch((err)=> {
          console.log(err);
          const Toast = swal.mixin({
              toast: true,
              position: 'bottom-start',
              customClass: 'sweet-modal',
              showConfirmButton: false,
              timer: 3000
          });
          Toast.fire({
            title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_cover_not_uploaded')
          });
        });
      },
      postProfileImage(name, blob) {
        let formData = new FormData();
        formData.append('file', blob, name);
        this.USER_AVATAR_SEND({
          image: formData,
          isProfile: true
        }).then((res)=>{
            console.log(res);
            this.$store.dispatch(USER_REQUEST)
              .then((resp)=>console.log(resp))
              .catch((err)=>console.log(err));
            const Toast = swal.mixin({
              toast: true,
              position: 'bottom-start',
              customClass: 'sweet-modal',
              showConfirmButton: false,
              timer: 3000
            });
            Toast.fire({
              title: '<i class="fa fa-check fa-2x"></i>' + this.$t('pr_avatar_uploaded')
            });
        })
        .catch((err)=> {
          console.log(err);
          const Toast = swal.mixin({
              toast: true,
              position: 'bottom-start',
              customClass: 'sweet-modal',
              showConfirmButton: false,
              timer: 3000
          });
          Toast.fire({
            title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_avatar_not_uploaded')
          });
        });
      },
      onFileTypeMismatch () {
        this.coverFiles = this.$t('pr_no_file');
        const Toast = swal.mixin({
            toast: true,
            position: 'bottom-start',
            customClass: 'sweet-modal',
            showConfirmButton: false,
            timer: 3000
        });
        Toast.fire({
          title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_invalid_file')
        });
      },
      onFileSizeExceed () {
        this.coverFiles = this.$t('pr_no_file');
        const Toast = swal.mixin({
            toast: true,
            position: 'bottom-start',
            customClass: 'sweet-modal',
            showConfirmButton: false,
            timer: 3000
        });
        Toast.fire({
          title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_big_file')
        });
      },
      onInitCropImage() {
        this.cropPhoto.addClipPlugin(function (ctx, x, y, w, h) {
          ctx.beginPath()
          ctx.arc(x + w / 2, y + h / 2, w / 2, 0, 2 * Math.PI, true)
          ctx.closePath()
        })  
      },
      coverLoaded() {
        this.coverFiles = this.cropCover.$el.firstElementChild.files[0].name;
        if(this.coverFiles.length > 20) {
          this.coverFiles = this.coverFiles.substring(0, 18)+"..";
        }
      },
      isNumber(evt) {
        if (evt.type === 'paste') {
          evt.preventDefault();
        }
        evt = (evt) ? evt : window.event;
        var charCode = (evt.which) ? evt.which : evt.keyCode;
        if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
          evt.preventDefault();
        } else {
          return true;
        }
      },
      isEmailValid() {
        return (this.user.email == "") ? "" : (this.regexEmail.test(this.user.email)) ? 'has-success' : 'has-error';
      },
      updateUserProfile() {
        if (this.user.isAgreementAccepted) {
          this.USER_SEND({
            id: this.getUserProfile.id,
            user: this.user
          }).then((res)=> {
              console.log(res);
              this.showAgreementModal = false;
              const Toast = swal.mixin({
                toast: true,
                position: 'bottom-start',
                customClass: 'sweet-modal',
                showConfirmButton: false,
                timer: 3000
              });
              Toast.fire({
                title: '<i class="fa fa-check fa-2x"></i>' + this.$t('pr_successfully_saved')
              });
            })
            .catch((err)=> {
              console.log(err);
              const Toast = swal.mixin({
                toast: true,
                position: 'bottom-start',
                customClass: 'sweet-modal',
                showConfirmButton: false,
                timer: 3000
              });
              Toast.fire({
                title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_not_saved')
              });
            });
        }
      },
      deleteUserProfile() {
        swal.fire({
          title: this.$t('pr_delete_profile_question'),
          text: this.$t('pr_delete_profile_txt'),
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#FB404B',
          confirmButtonText: this.$t('pr_delete_ok'),
          cancelButtonText: this.$t('pr_delete_cancel'),
          cancelButtonClass: 'profile-delete-cancel'
        }).then((result) => {
          if (result.value) {
            this.USER_DELETE().then((res)=> {
              console.log(res);
              swal.fire({
                title: this.$t('pr_delete_successfull_header'),
                text: this.$t('pr_delete_successfull_txt'),
                type: 'success',
              }).then(() => {
                this.$store.dispatch(AUTH_LOGOUT);
                this.$router.push({name: 'home'});
              });
            })
            .catch((err)=> {
              console.log(err);
              swal.fire({
                title: this.$t('pr_delete_error_header'),
                text: this.$t('pr_delete_error_txt'),
                type: 'error',
              });
            });     
          }
        });
      },
      getNickName() {
        let index = this.user.email.indexOf('@');
        if (index !== -1) {
          this.user.nickName = this.user.email.substring(0, index);
        }
      }
    }
  }
</script>

