<template>
  <b-navbar toggleable="md" fixed="top">
    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
    <b-collapse is-nav id="nav_collapse">
      <b-navbar-nav class="ml-auto">
        <div>
          <b-input-group size="sm" v-if="isLogin">
            <b-input-group-prepend>
              <b-btn :variant="isMobileResolution ? 'secondary' : 'outline-secondary'" @click="onSubmit">{{$t('search')}}</b-btn>
            </b-input-group-prepend>
            <b-form-input @keydown.native="onKeyPress" v-model="searchText"></b-form-input>
          </b-input-group>
        </div>
        <b-nav-item to="" @click="home"><i class="fa fa-home"></i> {{$t('homePage')}}</b-nav-item>
        <b-nav-item v-if="!isLogin" :to="{name: 'login'}"><i class="fa fa-sign-in"></i> {{$t('logIn')}}</b-nav-item>
        <b-nav-item v-if="isLogin" to="" @click="logOut"><i class="fa fa-sign-out"></i> {{$t('logOut')}}</b-nav-item>
        <b-nav-item-dropdown right color="red">
          <span slot="button-content" class="myRed">
            <i class="fa fa-globe"></i><span> {{$t('language')}}</span>
          </span>
          <b-dropdown-item v-for="lng in languages" href="#" @click="setLang(lng[0])" :key="lng[0]">
            <img :src="`/img/flags/${getImgName(lng[0].toUpperCase())}.png`" style="padding-right: 10px"/>{{lng[1]}}
          </b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>

    </b-collapse>
  </b-navbar>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { LANG_SETLNG } from '../store/actions/langConst'
  import {AUTH_LOGOUT} from "../store/actions/userConst";

  export default {
    data () {
      return {
        searchText: '',
      }
    },
    computed: {
      ...mapGetters(['languages', 'isLogin', 'isMobileResolution'])
    },
    methods: {
      onSubmit() {
        // this.$router.push({name: 'home'})
        this.$router.push({name: 'search', query: { q: this.searchText } })
      },
      onKeyPress (evt) {
        if(evt.code==='Enter' || evt.code==='NumpadEnter')
          this.onSubmit()
      },
      home() {
        this.$router.push({name: 'home'});
      },
      logOut() {
        this.$store.dispatch(AUTH_LOGOUT).then(() => {
          this.$router.push({name: 'home'});
        });
      },
      getImgName(lng) {
        return lng == 'EN'?'GB':lng;
      },
      setLang(lang) {
        this.$store.dispatch(LANG_SETLNG, lang);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .navbar {
    z-index: 10 !important;
  }
  .nav-link {
    color: rgba(0, 0, 0, 0.5) !important;
    &:hover {
      color: rgba(0, 0, 0, 1) !important;
    }
  }
  @media (min-width: 768px) {
    .navbar-expand-md {
      background-color: white;
      border-bottom: 1px solid rgba(0,0,0,.1);
      padding-right: 28px !important;
    }
  }
  @media (max-width: 768px) {
    .fixed-top {
      position: relative !important;
    }
    a.nav-link,
    span {
      color: rgba(255,255,255,.9) !important;
    }
  }
</style>

