  [uConst.POSTS_LOAD]: ({commit}, id, whereParams) => {
    return new Promise((resolve, reject) => {
      commit(uConst.POSTS_REQUEST)
      // {"include": {"relation": "users", "scope": {"fields": ["firstName","lastName"]}}}
      axios.get(state.loopbackApi+'/Posts', {
        params: {
          filter: {
            order: 'created DESC', 
            include: {
              relation: 'users', 
              scope: {
                fields: ['id','firstName','lastName']
              }
            },
            where: whereParams
          }
        }
      })
      // axios.get(state.loopbackApi+'/Posts', {params: {filter: {order: 'created DESC', where: {usersId: state.userProfile.id}}}})
        // .then(x => new Promise(resolve => setTimeout(() => resolve(x), 5000))) // Задержка ответа
        .then((res)=>{
          // Получаем количество "своих" постов
          let now = new Date()
          let now1 = new Date()
          now1.setDate(now1.getDate()+1)
          axios.get(state.loopbackApi+`/Users/${id}/posts/count`, {
            params: {
              where: {created: {between: [now.toISOString().split('T')[0],now1.toISOString().split('T')[0]]}}
            }
          })
            .then((resC) => {
              commit(uConst.POSTS_SUCCESS)
              resolve({posts: res.data, myPostsCount: resC.data.count})
            })
        })
        .catch(err=>{
          commit(uConst.POSTS_ERROR)
          reject(err)
        })
    })
  },
