<template>
  <div>
    <div v-if="isLoading" class="stopUserEvents"></div>
    <b-modal class="testClass" v-model="modalShow" hide-header hide-footer @hidden="onHidden" centered hide-backdrop size="sm"
    ref="modalAuth" no-close-on-backdrop :no-close-on-esc="isLoading">
      <button type="button" aria-label="Close" class="close" @click="$refs.modalAuth.hide()">×</button>
      <form @submit.prevent="submit">
        <slot></slot>
      </form>
    </b-modal>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { SET_ROUTEBACK } from '../store/actions/otherConst';
  import {
    AUTH_LOGOUT,
    AUTH_REQUEST,
    USER_CHANGE_PASSWORD,
    USER_RESET_EMAIL,
    USER_RESET_PASSWORD
  } from '../store/actions/userConst'
  import { USER_REGISTER } from '../store/actions/userConst'
  import swal from 'sweetalert2'

  export default {
    props: {
      modalType: '',
      data: {}
    },
    data() {
      return {
        modalShow: true
      }
    },
    computed: {
      ...mapGetters(['routeBack', 'isLoading', 'isLogin'])
    },
    methods: {
      onHidden() {
        if (this.routeBack.isBack) {
          this.$store.dispatch(SET_ROUTEBACK, {isBack: false, path: ''})
          this.$router.go(-1);
        } else {
          this.$router.push({name: 'home'});
        }
      },
      submit() {
        switch (this.modalType) {
          case 'login':
            // this.$store.dispatch(AUTH_REQUEST, { email: '1@ya.ru', password: '1' })
            this.$store.dispatch(AUTH_REQUEST, this.data)
              .then(resp => {
                console.log(resp)
                this.$router.push({name: 'jobAds'})
              })
              .catch(err => {
                console.log(err)
                const code = this.$ObjValidate(() => err.response.data.error.code) ? err.response.data.error.code : '';
                switch (code) {
                  case 'LOGIN_FAILED_EMAIL_NOT_VERIFIED':
                    swal({
                      type: 'error',
                      text: 'Адрес Email не верифицирован, пожалуйста проверьте Вашу почту для активации аккаунта',
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                    break
                  case 'LOGIN_FAILED':
                    swal({
                      type: 'error',
                      text: this.$t('loginIncorrect'),
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                    break
                  default:
                    swal({
                      type: 'error',
                      text: 'Неизвестная ошибка. Обратитесь по адресу admin@universtage.com',
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                    break
                }
              })
            break
          case 'register':
            if (this.data.password === this.data.passwordConfirm) {
              // this.$store.dispatch(USER_REGISTER, {email: '1@ya.ru', password: '1', firstName: '1', locale: 'ru'})
              this.$store.dispatch(USER_REGISTER, this.data)
                .then(() => {
                  swal({
                    type: 'success',
                    text: 'Вы успешно зарегистрированы! На Ваш Email отправлена ссылка для верификации, пройдите по ней. Спасибо!',
                    keydownListenerCapture: true,
                    allowOutsideClick: false
                  })
                    .then(()=>this.$router.push({name: 'home'}))
                })
                .catch((err) => {
                  console.log(err)
                  const message = this.$ObjValidate(() => err.response.data.error.message) ? err.response.data.error.message : '';
                  swal({
                    type: 'error',
                    text: ~message.indexOf('Email already exists') ? 'Указанный Email уже существует' : 'Неизвестная ошибка',
                    keydownListenerCapture: true,
                    allowOutsideClick: false
                  })
                })
            } else {
              swal({
                type: 'error',
                text: 'Пароли не совпадают',
                keydownListenerCapture: true,
                allowOutsideClick: false
              })
            }
            break
          case 'reset':
            // this.$store.dispatch(USER_RESET_EMAIL, 'avlosev@yandex.ru')
            this.$store.dispatch(USER_RESET_EMAIL, this.data.email)
              .then(() => {
                swal({
                  type: 'success',
                  text: 'На ваш email выслана ссылка для восстановдения пароля. Внимание! Срок действия ссылки ограничен 15 минутами.',
                  keydownListenerCapture: true,
                  allowOutsideClick: false
                })
                  .then(() => this.$router.push({name: 'home'}))
              })
              .catch(err => {
                const code = this.$ObjValidate(() => err.response.data.error.code) ? err.response.data.error.code : '';
                switch (code) {
                  case 'EMAIL_NOT_FOUND':
                    swal({
                      type: 'error',
                      text: 'Пользователь с указанным Email не найден!',
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                    break
                  case 'EAUTH':
                    swal({
                      type: 'error',
                      text: 'Не удалось отправить ссылку на Ваш Email. Обратитесь по адресу admin@universtage.com',
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                    break
                  case 'RESET_FAILED_EMAIL_NOT_VERIFIED':
                    swal({
                      type: 'error',
                      text: 'Ваш Email не проверен. Проверьте почту с линком для проверки Email.',
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                    break
                  default:
                    swal({
                      type: 'error',
                      text: 'Неизвестная ошибка. Обратитесь по адресу admin@universtage.com',
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                    break
                }
              })
            break
          case 'resetPassword':
            if (this.isLogin) {
              this.$store.dispatch(USER_CHANGE_PASSWORD, {
                oldPassword: this.data.password,
                newPassword: this.data.passwordConfirm
              })
                .then(() => {
                  swal({
                    type: 'success',
                    text: 'Пароль успешно изменен',
                    keydownListenerCapture: true,
                    allowOutsideClick: false
                  })
                    .then(() => {
                      this.$store.dispatch(AUTH_LOGOUT)
                      this.$router.push({name: 'login'})
                    })
                })
                .catch(err => {
                  const code = this.$ObjValidate(() => err.response.data.error.code) ? err.response.data.error.code : '';
                  switch (code) {
                    case 'INVALID_PASSWORD':
                      swal({
                        type: 'error',
                        text: 'Введен неправильный старый пароль',
                        keydownListenerCapture: true,
                        allowOutsideClick: false
                      })
                      break
                    default:
                      swal({
                        type: 'error',
                        text: 'Неизвестная ошибка. Обратитесь по адресу admin@universtage.com',
                        keydownListenerCapture: true,
                        allowOutsideClick: false
                      })
                      break
                  }
                })
            } else {
              if (this.data.password === this.data.passwordConfirm) {
                this.$store.dispatch(USER_RESET_PASSWORD, {
                  newPassword: this.data.password,
                  accessToken: this.data.access_token
                })
                  .then(() => {
                    swal({
                      type: 'success',
                      text: 'Пароль успешно установлен',
                      keydownListenerCapture: true,
                      allowOutsideClick: false
                    })
                      .then(()=>this.$router.push({name: 'login'}))
                  })
                  .catch(err => {
                    const code = this.$ObjValidate(() => err.response.data.error.code) ? err.response.data.error.code : '';
                    switch (code) {
                      case 'INVALID_TOKEN':
                        swal({
                          type: 'error',
                          text: 'Неправильный токен. Возможно прошли отведенные 15 минут для установки пароля.',
                          keydownListenerCapture: true,
                          allowOutsideClick: false
                        })
                        break
                      default:
                        swal({
                          type: 'error',
                          text: 'Неизвестная ошибка. Обратитесь по адресу admin@universtage.com',
                          keydownListenerCapture: true,
                          allowOutsideClick: false
                        })
                        break
                    }
                  })
              } else {
                swal({
                  type: 'error',
                  text: 'Пароли не совпадают',
                  keydownListenerCapture: true,
                  allowOutsideClick: false
                })
              }
            }
            break
        }
      }
    },
  }
</script>

<style lang="scss">
  .form-control:focus {
    border-color: inherit !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
  }
  .btn-wd {
    min-width: 180px !important;
    background-color: #FB404B !important;
    border-color: #FB404B !important;
    border-radius: 30px !important;
    font-weight: 500 !important;
  }
  .btn-wd:hover {
    background-color: rgb(253, 36, 50) !important;
  }
  .modal-content {
    border-radius: 10px !important;
    background-color: white;
    opacity: 0.8;
  }
  .testClass { }

  @media (max-height: 565px) {
    .modal{
      margin-top:15%;
    }
  }
  .stopUserEvents {
    position: fixed;
    top: -10000px;
    left: -10000px;
    width: 20000px;
    height: 20000px;
    z-index: 999999;
  }
</style>
