<template>
  <router-view/>
</template>

<script>
    // @ is an alias to /src
    import { SET_WINDOWWIDTH } from './store/actions/otherConst'
    import { USER_REQUEST } from './store/actions/userConst'

    export default {
      created() {
        this.$store.dispatch(USER_REQUEST)
          .then((resp)=>console.log(resp))
          .catch((err)=>console.log(err))
        this.handleResize();
        window.addEventListener('resize', this.handleResize);
      },
      beforeDestroy: function () {
        window.removeEventListener('resize', this.handleResize)
      },
      methods: {
        handleResize() {
          this.$store.dispatch(SET_WINDOWWIDTH, document.documentElement.clientWidth);
        },
      },
    }
</script>

