<template>
  <modal-auth modal-type="register" :data="data">
    <h5 class="text-center">{{$t('register')}}</h5>
    <br>
    <input type="email" class="form-control login-input" :placeholder="$t('enterEmail')" v-model="data.email" required/>
    <input type="text" class="form-control login-input" :placeholder="$t('yourFirstName')" v-model="data.firstName" required/>
    <input type="text" class="form-control login-input" :placeholder="$t('yourLastName')" v-model="data.lastName"/>
    <input type="password" class="form-control login-input" :placeholder="$t('password')" v-model="data.password" required/>
    <input type="password" class="form-control login-input" :placeholder="$t('passwordConfirmation')" v-model="data.passwordConfirm" required/>
    <select class="form-control login-input" v-model="data.userType" required>
      <option class="option-select" value="" selected>{{ $t('reg_select_type') }}</option>
      <option value="artist">{{ $t('reg_artist_type') }}</option>
      <option value="team">{{ $t('reg_team_type') }}</option>
      <option value="agent">{{ $t('reg_agent_type') }}</option>
    </select>

    <div class="text-left">
      <div class="form-group has-label">
          <input type="checkbox" name="agreementCheckbox" id="youtubeRadio" v-model="data.isAgreementAccepted" required/>
          <label class="form-checkbox" for="youtubeRadio">
            <span>
            {{ $t('reg_agreement') }} <a href="/terms-and-condition" target="_blank">{{ $t('reg_agreement_part') }}</a> <span class="require-sign">*</span>
            </span>
          </label>
      </div>
    </div>
                                          
    <div class="text-center">
      <button v-if="!isLoading" class="btn btn-fill btn-danger btn-round btn-sm btn-wd full-width btn-register" type="submit">{{$t('createAccount')}}</button>
      <sync-loader v-if="isLoading" class="text-center" color="red"></sync-loader>
    </div>
  </modal-auth>
</template>

<script>
  import ModalAuth from '../components/ModalAuth'
  import syncLoader from 'vue-spinner/src/PulseLoader.vue'
  import { mapGetters } from 'vuex';

  export default {
    data() {
      return {
        data: {
          email: '',
          password: '',
          passwordConfirm: '',
          firstName: '',
          lastName: '',
          userType: '',
          isAgreementAccepted: false
        }
      }
    },
    computed: {
      ...mapGetters(['isLoading'])
    },
    components: { ModalAuth, syncLoader },
  }
</script>

<style scoped>
.form-checkbox {
    font-size: 20px !important;
}
.form-checkbox span {
  font-size: 0.8rem;
  line-height: 1.2;
  color: black;
  padding-left: 5px;
  padding-right: 15px;
  display: inline-block;
  position: absolute;
}
.form-checkbox span a {
  color: black !important;
  text-decoration: underline;
}
.btn-register {
  margin-top: 20px;
}
div.form-group.has-label {
  margin-bottom: 0 !important;
}
.require-sign {
  color: red !important;
}
</style>
