<template>
  <modal-auth modal-type="login" :data="data">
    <h5 class="text-center">{{$t('logIn')}}</h5>
    <br>
    <p class="login-label">{{$t('emailAddressUpper')}} <span style="color: red">*</span></p>
    <input type="email" class="form-control login-input" :placeholder="$t('enterEmail')" v-model="data.email" required/>
    <p class="login-label">{{$t('passwordUpper')}} <span style="color: red">*</span></p>
    <input type="password" class="form-control" :placeholder="$t('password')" v-model="data.password" required/>
    <br>
    <div class="text-center">
      <button v-if="!isLoading" class="btn btn-fill btn-danger btn-round btn-sm btn-wd full-width" type="submit">{{$t('logIn')}}</button>
      <sync-loader v-if="isLoading" class="text-center" color="red"></sync-loader>
    </div>
    <div class="remember-block">
      <router-link :to="{name: 'register'}" class="link">{{$t('register')}}</router-link>
      <span class="stick">|</span>
      <router-link :to="{name: 'reset'}" class="link">{{$t('forgotYourPassword')}}</router-link>
    </div>
    <div class="text-center">
      <button type="button" class="btn btn-info btn-fill btn-wd btn-social facebook" @click="authFacebook()">
          <i class="fa fa-facebook"></i> <span class="btn-txt">{{$t('logIn')}}</span>
      </button>
      <button type="button" class="btn btn-info btn-fill btn-wd btn-social facebook" @click="authVkontakte()">
          <i class="fa fa-vk"></i> <span class="btn-txt">{{$t('logIn')}}</span>
      </button>
      <button type="button" class="btn btn-info btn-fill btn-wd btn-social" @click="authGoogle()">
          <i class="fa fa-google-plus"></i> <span class="btn-txt">{{$t('logIn')}}</span>
      </button>
    </div>

  </modal-auth>
</template>

<script>
  import ModalAuth from '../components/ModalAuth'
  import syncLoader from 'vue-spinner/src/PulseLoader.vue'
  import { mapGetters } from 'vuex';

  export default {
    data() {
      return {
        data: {
          email: '',
          password: ''
        },
        loopbackBase: process.env.VUE_APP_USTAGE_BASE || 'http://localhost:3000',
      }
    },
    computed: {
      ...mapGetters(['isLoading'])
    },
    methods: {
      authFacebook() {
         window.open(this.loopbackBase + '/auth/facebook', '_self');
      },
      authVkontakte() {
         window.open(this.loopbackBase + '/auth/vkontakte', '_self');
      },
      authGoogle() {
         window.open(this.loopbackBase + '/auth/google', '_self');
      }
    },
    components: { ModalAuth, syncLoader },
  }
</script>

<style>
.btn-social {
    min-width: 1px !important;
    width: 45px;
    transition: all 0.3s !important;
    margin-left: 3px;
    margin-right: 3px;
    border: 1px solid #ff7777 !important;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.btn-social:hover {
    width: 115px !important;
}
.btn-social .fa {
    font-size: 20px;
    padding-left: 0px;
    padding-top: 4px;
    padding-bottom: 4px;
    text-align: center;
}
.btn-social .btn-txt {
    position: relative;
    bottom: 2px;
    margin-left: 10px;
    display: none;
}
.btn-social:hover .btn-txt {
    display: inline;
}
a.link {
  font-size: 0.9rem;
  color: black; 
  text-decoration: underline;
}
a.link:hover {
  color: black;
}
span.stick {
  color: gray;
  padding-left: 5px;
  padding-right: 5px;
}
button.full-width {
  width: 100%;
  font-size: 1rem !important;
}
.remember-block {
  text-align: center;
  padding-top: 5px;
  padding-bottom: 10px;
}
.login-label {
  margin-bottom: 10px !important;
}
.btn-social.facebook {
  background-color: #4267B2 !important;
  border-color: #4267B2 !important;
}
.btn-social.vkontakte {
  background-color: #4A76A8 !important;
  border-color: #4A76A8 !important;
}
.btn-social.google {
  background-color: #EA4335 !important;
  border-color: #EA4335 !important;
}
.login-input {
  margin-bottom: 15px;
}
</style>