<template>
  <div>
    <div class="row">
      <div class="col-md-4">
        <label>{{$t('artistType').toUpperCase()}}</label>
        <select class="form-control" v-model="selectArtistType" @change="changeSelect(true)">
          <option :disabled="selectArtistType" :value="null">- {{$t('type')}} -</option>
          <option v-for="type in getTypes" :value="type" :key="type.type_en">
            {{ type[`type_${currentLng}`] }}
          </option>
        </select>
      </div>
      <div class="col-md-4">
        <label>{{$t('categories').toUpperCase()}}</label>
        <select class="form-control" v-model="selectArtistCategories" @change="changeSelect()">
          <option :value="null">- {{$t('category')}} -</option>
          <option v-for="cat in getGategories" :value="cat" :key="cat.name_en">
            {{ cat[`name_${currentLng}`] }}
          </option>
        </select>
      </div>
      <div class="col-md-4">
        <label>{{$t('skillsAndStyles').toUpperCase()}}</label>
        <select class="form-control" v-model="selectArtistSkill">
          <option :value="null">- {{$t('skillOrStyle')}} -</option>
          <option v-for="skill in getSkills" :value="skill" :key="skill.skill_en">
            {{ skill[`skill_${currentLng}`] }}
          </option>
        </select>
      </div>
    </div>

    <div class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4 text-center">
          <button type="button" class="btn btn-default btn-add" :disabled="!selectArtistSkill" @click="addSkill">{{$t('pr_add_type')}}</button>        
      </div>
      <div class="col-md-4"></div>
    </div>

    <div class="row">
        <div class="col-md-12 disable-col-padding-m">
          <div class="card skills-container" v-if="data.selectedSkills && data.selectedSkills.length">
            <div class="skills-list">
              <div v-for="(skill, index) in data.selectedSkills" :key="index">
                <span v-if="!skill.deleted"> 
                  {{getSkillString(skill)}} <i class="fa fa-times fa-size2" v-on:click="deleteSkill(skill, index)"></i>
                </span>
              </div>
            </div>
          </div>
        </div>
    </div>
</div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';
  import {GET_ARTIST_TYPES, GET_SKILLS} from "../store/actions/skillsConst";

  export default {
    name: "SelectSkills",
    props: {
      data: {},
    },
    created() {
      this.GET_ARTIST_TYPES()
        .then(res=>{
          this.artistTypes=res
          this.$emit('load-artist-types',res)
        })
        .catch(err=>console.log(err))
      this.GET_SKILLS()
        .then(res=>{
          this.skills=res
          this.$emit('load-skills',res)
        })
        .catch(err=>console.log(err))
    },
    data() {
      return {
        skills: [],
        artistTypes: [],
        selectArtistType: null,
        selectArtistCategories: null,
        selectArtistSkill: null,
        //selectedSkills: [],
      }
    },
    computed: {
      ...mapGetters(['currentLng', 'getUserProfile', 'isLoadingPosts']),
      getSkills() {
        let allSkill = this.selectArtistCategories ? this.skills.filter(skill=>this.selectArtistCategories.skills.indexOf(skill.code)!=-1)
          .sort((a,b)=>a[`skill_${this.currentLng}`].localeCompare(b[`skill_${this.currentLng}`])): null
        if(allSkill) {
          let myFilter = this.data.selectedSkills.filter(s=>s.artistTypeId===this.selectArtistType.id && s.categories===this.selectArtistCategories.name_en);
          return allSkill.filter(a=>!~myFilter.findIndex(m=>m.skill===a.code))
        } else {
          return allSkill
        }
      },
      getGategories() {
        let data = JSON.parse(JSON.stringify(this.selectArtistType))
        return this.selectArtistType ? data.categories.sort((a,b)=>a[`name_${this.currentLng}`].localeCompare(b[`name_${this.currentLng}`])) : null
      },
      getTypes() {
        let data = JSON.parse(JSON.stringify(this.artistTypes))
        return data.sort((a,b)=>a[`type_${this.currentLng}`].localeCompare(b[`type_${this.currentLng}`]))
      },
    },
    methods: {
      ...mapActions([GET_SKILLS, GET_ARTIST_TYPES]),
      changeSelect(type) {
        if(type) {
          this.selectArtistCategories = null;
          this.selectArtistSkill = null;
        } else {
          let skills = this.getSkills;
          for(let i = 0, len = skills.length; i < len; i++) {
            if (skills[i].skill_en === 'Various') {
              this.selectArtistSkill = skills[i];
              break;
            }
          }
        }
      },
      addSkill() {
        this.data.selectedSkills.push({artistTypeId: this.selectArtistType.id, categories: this.selectArtistCategories.name_en, skill: this.selectArtistSkill.code})
        this.selectArtistSkill = null
        this.$emit('changed-selected-skills',this.data.selectedSkills)
      },
      deleteSkill(skill,index) {
        if(skill.id) {
          skill.deleted = true
          this.data.selectedSkills.splice(index,1,skill)
        } else {
          this.data.selectedSkills.splice(index,1)
        }
        this.$emit('changed-selected-skills',this.data.selectedSkills)
      },
      getSkillString(skill) {
        let aType = this.artistTypes.find(t=>t.id===skill.artistTypeId)
        if(aType) {
          let aCat = aType.categories.find(c=>c.name_en===skill.categories)
          if(aCat) {
            let aSkill = this.skills.find(s=>s.code===skill.skill)
            if(aSkill) {
              return `${aType['type_'+this.currentLng]} - ${aCat['name_'+this.currentLng]} - ${aSkill['skill_'+this.currentLng]}`
            } else {
              console.log(`Ненайден skill артиста с code=${skill.skill}`)
            }
          } else {
            console.log(`Ненайдена категория артиста с ID=${skill.artistTypeId} name_en=${skill.categories}`)
          }
        } else {
          console.log(`Ненайден тип артиста с ID=${skill.artistTypeId}`)
        }
        return 'Error!'
      },
    },
  }
</script>

