  }
  .form-control:-ms-input-placeholder,
  .form-control::-ms-input-placeholder {
      color: #6c757d;
      opacity: 1
  }
  .form-control::placeholder {
      color: #6c757d;
      opacity: 1
  }
  .un-date-picker .un-input .un-input-inside:-moz-placeholder,
  .un-date-picker .un-input .un-input-inside::-moz-placeholder,
  .form-control:-moz-placeholder,
  .form-control::-moz-placeholder,
  .form-group .un-input-inside:-moz-placeholder,
  .form-group .un-input-inside::-moz-placeholder {
      color: #ddd;
      opacity: 1;
      filter: alpha(opacity=100)
  }
  .un-date-picker .un-input .un-input-inside::-webkit-input-placeholder,
  .form-control::-webkit-input-placeholder,
  .form-group .un-input-inside::-webkit-input-placeholder {
      color: rgb(207, 207, 207);
      opacity: 1;
      filter: alpha(opacity=100)
  }
  .un-date-picker .un-input .un-input-inside:-ms-input-placeholder,
  .form-control:-ms-input-placeholder,
  .form-group .un-input-inside:-ms-input-placeholder {
      color: #ddd;
      opacity: 1;
      filter: alpha(opacity=100)
  }
  .form-control:focus {
    border-color: #aaa !important;
  }
  .form-control {
    background-color: #fff;
    border-radius: 4px;
    font-size: .975rem;
    color: #565656;
    padding: 8px 12px;
    height: 40px;
    box-shadow: none;
  }
  .form-control:disabled, {
    background-color: #e9ecef !important;
