  }
  .card label {
    font-size: 14px;
    margin-bottom: 5px;
    text-transform: uppercase;
    font-weight: 400;
    color: #828080;
    display: inline-block;
  }
  .skills-container {
    max-height: 200px;
    overflow-y: scroll;
    padding: 10px 15px 0px 15px;
    margin-bottom: 30px;
  }
  .skills-container::-webkit-scrollbar {
      width: 8px
  }
  .skills-list div {
