<template>
  <div v-if="isLoading"><sync-loader class="text-center" color="white"></sync-loader></div>
</template>

<script>
  import swal from 'sweetalert2'
  import {USER_CONFIRM_EMAIL} from "../store/actions/userConst";
  import syncLoader from 'vue-spinner/src/PulseLoader.vue'
  import { mapGetters } from 'vuex';

  export default {
    name: "Confirm",
    computed: {
      ...mapGetters(['isLoading'])
    },
    components: {syncLoader},
    props: {
      token: {type: String, default: null},
      uid: {type: String, default: null}
    },
    created() {
      if(this.uid && this.token) {
        this.$store.dispatch(USER_CONFIRM_EMAIL, {uid: this.uid, token: this.token})
          .then(()=>{
            swal({
              type: 'success',
              text: 'Ваш Email успешно подтвержден, входите в систему',
              keydownListenerCapture: true,
              allowOutsideClick: false
            })
              .then(()=>this.$router.push({name: 'login'}))
          })
          .catch(err=>{
            console.log(err)
            const code = this.$ObjValidate(() => err.response.data.error.code) ? err.response.data.error.code : '';
            switch (code) {
              case 'USER_NOT_FOUND':
                swal({
                  type: 'error',
                  text: 'Пользователь не найден. Обратитесь по адресу admin@universtage.com',
                  keydownListenerCapture: true,
                  allowOutsideClick: false
                })
                  .then(()=>this.$router.push({name: 'home'}))
                break
              case 'INVALID_TOKEN':
                swal({
                  type: 'error',
                  text: 'Не верный токен. Обратитесь по адресу admin@universtage.com',
                  keydownListenerCapture: true,
                  allowOutsideClick: false
                })
                  .then(()=>this.$router.push({name: 'home'}))
                break
              default:
                swal({
                  type: 'error',
                  text: 'Неизвестная ошибка. Обратитесь по адресу admin@universtage.com',
                  keydownListenerCapture: true,
                  allowOutsideClick: false
                })
                  .then(()=>this.$router.push({name: 'home'}))
                break
            }
          })
      } else {
        swal({
          type: 'error',
          text: 'Не корректная ссылка для подтверждения Email',
          keydownListenerCapture: true,
          allowOutsideClick: false
        })
        this.$router.push({name: 'home'})
      }
    }
  }
</script>

<style lang="scss" scoped>
  div {
    width: 100%;
    text-align: center;
  }
</style>