import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Register from './views/Register'
import TermsAndCondition from './views/TermsAndCondition'
import Reset from './views/Reset'
import ResetPassword from './views/ResetPassword'
import NF from './views/NotFound'
import Statistics from './views/Statistics'
import store from './store/store'
import JobAds from './views/JobAds'
import Confirm from "./views/Confirm";
import UpdateProfile from "./views/UpdateProfile.vue"
import Profile from "./views/Profile.vue"
import Posts from "./views/Posts";
import SocialLogin from "./views/SocialLogin";
import Search from "./views/Search";

Vue.use(Router);

const ifNotAuthenticated = (to, from, next) => {
  if (!store.getters.isLogin) {
    next();
    return;
  }
  next({name: 'home'});
};

const ifAuthenticated = (to, from, next) => {
  if (store.getters.isLogin) {
    next();
    return;
  }
  next({name: 'login'});
};

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL || '/',
  // base: '/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children: [
        {
          path: 'login',
          name: 'login',
          component: Login,
          beforeEnter: ifNotAuthenticated
        },
        {
          path: 'register',
          name: 'register',
          component: Register
        },
        {
          path: '/terms-and-condition',
          name: 'termsAndCondition',
          component: TermsAndCondition
        },
        {
          path: 'reset',
          name: 'reset',
          component: Reset
        },
        {
          path: 'reset-password',
          name: 'resetPassword',
          component: ResetPassword,
          props: (route) => ({access_token: route.query.access_token})
        },
        {
          path: 'confirm',
          name: 'confirm',
          component: Confirm,
          props: (route) => ({token: route.query.token, uid: route.query.uid})
        },
        {
          path: 'social-login',
          name: 'socialLogin',
          component: SocialLogin,
          beforeEnter: ifNotAuthenticated,
          props: (route) => ({access_token: route.query['access-token']})
        },
        {
          path: 'search',
          name: 'search',
          component: Search,
          props: (route) => ({ query: route.query.q })
        },
        {
          path: 'statistics/:type?',
          name: 'statistics',
          component: Statistics
        },
        {
          path: 'jobAds',
          name: 'jobAds',
          component: JobAds,
          beforeEnter: ifAuthenticated
        },
        {
          path: 'posts',
          name: 'posts',
          component: Posts,
          beforeEnter: ifAuthenticated
        },
        {
          path: 'updateProfile',
          name: 'updateProfile',
          component: UpdateProfile,
          beforeEnter: ifAuthenticated
        },
        {
          path: 'profile/:id',
          name: 'profile',
          component: Profile
        },
        {
          path: 'about',
          name: 'about',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
        },
      ]
    },
    { path: '*', component: NF },
    // { path: '/', redirect: to =>{
    //   return { name: 'home' }}
    // },
  ]
})
