<template>
  <side-bar-page>
    <template slot="content">
      <div id="elementEditPost" class="container-fluid" style="padding-top: 10px">
        <!-- <button @click="test">Test</button> -->
        <b-collapse id="filters" class="mt-2" v-model="showFilters">
          <div style="width: 100%;">
            <b-form-group :label="`${$t('selectTypeOfPosts')}:`">
              <b-form-checkbox-group plain v-model="filters.typeSeleted" :options="typeView"></b-form-checkbox-group>
            </b-form-group>
          </div>
          <div style="width: 100%;">
            <b-form-group :label="`${$t('selectFilters')}:`">
              <b-row>
                <b-col cols="12" sm="6">
                  <multiselect
                    v-model="filters.typesArtistSeleted" 
                    :options="getTypes"
                    :track-by="`type_${currentLng}`"
                    :placeholder="$t('selectTypesOfArtists')"
                    :custom-label="typesLabel"
                    @remove="removeType"
                    @select="selectType"
                    :searchable="false"
                  >
                    <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length &amp;&amp; !isOpen">{{ values.length }} {{$t('typesSelected')}}</span></template>                    
                  </multiselect>
                </b-col>
                <b-col cols="12" sm="6">
                  <multiselect
                    v-model="filters.categoriesArtistSeleted" 
                    :options="getCategories"
                    :track-by="`name_${currentLng}`"
                    :placeholder="$t('selectCategoriesOfArtists')"
                    :custom-label="categoriesLabel"
                    @remove="removeCategories"
                    @select="selectCategories"
                    :searchable="false"
                  >
                    <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length &amp;&amp; !isOpen">{{ values.length }} {{$t('categoriesSelected')}}</span></template>                    
                  </multiselect>
                </b-col>
                <b-col cols="12">
                  <multiselect
                    v-model="filters.skillsArtistSeleted" 
                    :options="getSkills"
                    :track-by="`skill_${currentLng}`"
                    :placeholder="$t('selectSkillsOfArtists')"
                    :custom-label="skillsLabel"
                    :searchable="false"
                  >
                    <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length &amp;&amp; !isOpen">{{ values.length }} {{$t('skillsSelected')}}</span></template>                    
                  </multiselect>
                </b-col>
              </b-row>
              <gmap-autocomplete :hidden="!~filters.typeSeleted.indexOf('agent')" ref="autocompleteFilters" name="autocompleteFilters" class="form-control" 
                :placeholder="$t('enterCitySearch')" :types="['(cities)']" @place_changed="setPlaceFilters" @change="clearGooglePlace">
              </gmap-autocomplete>
            </b-form-group>
          </div>
          <div style="width: 100%; text-align: center; margin-bottom: 10px;">
            <button class="btn btn-fill btn-danger btn-round btn-sm btn-wd" @click="loadPosts">
              {{$t('apply')}}
            </button>
          </div>
        </b-collapse>
        <br>
        <b-row :class="$style.colReverse">
          <div class="col-md-12">
              <button 
                :disabled="myPostsCount>2"
                @click="setShowCollapse" 
                class="btn btn-fill btn-danger btn-round btn-sm btn-wd btn-post">
                {{showCollapse ? $t('cancel') : $t('addPost')}}
              </button>
              <button 
                @click="showFilters=!showFilters" 
                class="btn btn-fill btn-danger btn-round btn-sm btn-wd btn-post">
                {{$t('filters')}}
              </button>
            <!-- <button @click="showFilters=!showFilters">Фильтры</button> -->
          </div>
        </b-row>
        <div v-if="myPostsCount>2" style="width: 100%; text-align: center;">{{$t('text14')}}</div>
        <b-collapse id="collapse1" class="mt-2" v-model="showCollapse">
          <b-card>
            <b-row>
              <b-col>
                <circle-button :iAmArtist="true" :selectButton="isLookingJob" @click="isLookingJob=true"/>
              </b-col>
              <b-col>
                <circle-button :iAmArtist="false" :selectButton="!isLookingJob" @click="isLookingJob=false"/>
              </b-col>
            </b-row>
            <select-skills @load-artist-types="artistTypes=$event" @load-skills="skills=$event" @change-sected-skills="selectedSkills=$event"
                           :data="{selectedSkills: selectedSkills}"/>
            <textarea v-model="text" :placeholder="$t('enterText')" class="form-control" rows="5" style="margin-bottom: 10px;" maxlength="1000"></textarea>
            <b-row style="margin-bottom: 10px;">
              <b-col>
                <div class="form-group has-label">
                    <label>
                        {{$t('from')}}
                    </label>
                    <datepicker :format="dateFormatter" :language="calendarLng()" v-model="date_start" :placeholder="$t('pr_placeholder_date')" name="dateBirthday"></datepicker>
                </div>
              </b-col>
              <b-col>
                <div class="form-group has-label">
                    <label>
                        {{$t('to')}}
                    </label>
                    <datepicker :format="dateFormatter" :language="calendarLng()" v-model="date_end" :placeholder="$t('pr_placeholder_date')" name="dateBirthday"></datepicker>
                </div>
              </b-col>
            </b-row>
            <!-- <gmap-autocomplete v-if="!isLookingJob" ref="autocomplete" class="form-control" :placeholder="$t('enterCity')"  -->
            <gmap-autocomplete :hidden="isLookingJob" ref="autocomplete"  name="autocomplete" class="form-control" :placeholder="$t('enterCity')" 
              :types="['(cities)']" @place_changed="setPlace" @change="clearGooglePlace">
            </gmap-autocomplete>
            <b-button variant="outline-danger" @click="sendPost" :class="{[$style.buttonDisabled]: !text.length}" :disabled="!text.length">{{$t('send')}}</b-button>
          </b-card>
        </b-collapse>
        <div v-if="isLoadingPosts">
          {{$t('loading')}}...
          <spinner-loader color="red"></spinner-loader>
        </div>
        <div v-if="!isLoadingPosts && posts.length" style="padding: 20px;">
          <b-row class="card card-post" v-for="(post,index) in posts" :key="index" :id="'post'+index">
            <div class="col-md-12">
              <div class="control-btn" v-if="post.usersId===getUserProfile.id">
                <b-button size="sm" variant="outline-danger" :title="$t('edit')" @click="editPost(post)"><i class="fa fa-edit"></i></b-button>
                <b-button size="sm" variant="outline-danger" :title="$t('delete')" @click="deletePost(post,index)"><i class="fa fa-trash"></i></b-button>
              </div>
            </div>
            <router-link :to="{name: 'profile', params: {id: post.users.id}}">
              <div style="width: 100%;">
                <img :src="post.users.avatar" class="image-avatar"/>{{post.users.firstName}} {{post.users.lastName}}
              </div>
            </router-link>
            <div class="col-md-12 disable-col-padding status-post">
              <div v-if="post.participantType === 1"><i class="fa fa-check fa-post"></i>{{$t('availableForContract')}}</div>
              <div v-if="post.participantType !== 1"><i class="fa fa-search fa-post"></i> {{$t('lookingFor')}}</div>
            </div>
            <div class="col-md-12 disable-col-padding">
              <div v-if="post.artistTypesList.length" style="width: 100%;">{{$t('skillOrStyle')}}:</div>
              <div v-if="post.artistTypesList.length" class="skills-post">
                <p v-for="(skill, index) in post.artistTypesList" :key="index">{{getSkillString(skill)}}</p>
              </div>
              <div v-for="(text,index) in post.localeTextsList" :key="index" class="message-post">
                {{text.text}}
              </div>
              <div v-if="post.date_start" class="date-post">
                <i class="fa fa-calendar-o fa-post"></i> {{$t('from')}}: {{onlyDate(post.date_start)}}{{post.date_end ? ` ${$t('to')}: ${onlyDate(post.date_end)}` : ''}}
              </div>
              <div v-if="post.googlePlace" class="location-post">
                {{$t('location')}}: {{post.googlePlace.formatted_address}}
              </div>
            </div>
          </b-row>
        </div>
      </div>
    </template>
  </side-bar-page>
</template>

<script>
  import Multiselect from 'vue-multiselect'
  import 'vue-multiselect/dist/vue-multiselect.min.css'
  import { mapActions, mapGetters } from 'vuex'
  import CircleButton from '../components/CircleButton'
  import SideBarPage from '../components/SideBarPage'
  import SpinnerLoader from 'vue-spinner/src/PulseLoader.vue'
  import {POST_DELETE, POST_SEND, POSTS_LOAD, USER_AVATAR_LOAD} from "../store/actions/userConst"
  import { interval } from 'rxjs'
  import { takeUntil, filter } from 'rxjs/operators'
  import SelectSkills from "../components/SelectSkills"
  import swal from 'sweetalert2'
  import datepicker from 'vuejs-datepicker';
  import {en, ru} from 'vuejs-datepicker/dist/locale';
  var moment = require('moment');
  moment().format();

  export default {
    created() {
      const obs = interval(1000)
      obs
        .pipe(takeUntil(obs.pipe(filter(val => {
          if(this.getUserProfile.id && this.artistTypes.length && this.skills.length) {
            this.loadPosts()
            return true
          } else {
            if(val>30) {
              console.log('time out 30s. for load profile')
              return true
            }
          }
        }))))
        .subscribe(()=>{})
    },
    data() {
      return {
        post: {},
        posts: [],
        isLookingJob: true,
        skills: [],
        artistTypes: [],
        selectedSkills: [],
        showCollapse: false,
        date_start: null,
        date_end: null,
        text: '',
        postId: null,
        googlePlace: null,
        cacheAvatars: [],
        showFilters: false,
        myPostsCount: 0,
        filters: {
          typeSeleted: ['artist','agent'],
          typesArtistSeleted: [], categoriesArtistSeleted: [], skillsArtistSeleted: [],
          googlePlace: null,
        },
      }
    },
    computed: {
      ...mapGetters(['currentLng', 'getUserProfile', 'isLoadingPosts', 'getAccessToken']),
      postSend() {
        return {
          id: this.post.id,
          usersId: this.getUserProfile.id,
          participantType: !this.isLookingJob + 1,
          googlePlace: this.googlePlace,
          localeTexts: this.post.id ? [{id: this.post.localeTextsList[0].id, locale: this.post.localeTextsList[0].locale, text: this.text}] :
            [{locale: this.currentLng, text: this.text}],
          artistTypes: this.selectedSkills,
          date_start: this.date_start,
          date_end: this.date_end,
          created: this.post.created,
        }
      },
      getTypes() {
        let data = JSON.parse(JSON.stringify(this.artistTypes))
        return data.sort((a,b)=>a[`type_${this.currentLng}`].localeCompare(b[`type_${this.currentLng}`]))
      },
      getCategories() {
        let res = [];
        if(Array.isArray(this.filters.typesArtistSeleted)) {
          this.filters.typesArtistSeleted.forEach(t=>{
            t.categories.forEach(c=>res.push(JSON.parse(JSON.stringify(c))))
          })
        } else {
          if(this.filters.typesArtistSeleted) {
            this.filters.typesArtistSeleted.categories.forEach(c=>res.push(JSON.parse(JSON.stringify(c))))
          }
        }
        return res.sort((a,b)=>a[`name_${this.currentLng}`].localeCompare(b[`name_${this.currentLng}`]))
      },
      getSkills() {
        let skillsList = [];
        if(Array.isArray(this.filters.categoriesArtistSeleted)) {
          this.filters.categoriesArtistSeleted.forEach(c=>{
            c.skills.forEach(s=>{
              if(skillsList.indexOf(s)===-1) skillsList.push(s)
            })
          })
        } else {
          if(this.filters.categoriesArtistSeleted) {
            this.filters.categoriesArtistSeleted.skills.forEach(s=>{
              if(skillsList.indexOf(s)===-1) skillsList.push(s)
            })
          }
        }
        return this.skills.filter(s=>skillsList.indexOf(s.code)!=-1).sort((a,b)=>a[`skill_${this.currentLng}`].localeCompare(b[`skill_${this.currentLng}`]))
      },
      typeView() {
        return [{text: this.$t('text12'), value: 'artist'},{text: this.$t('text13'), value: 'agent'}]
      },
    },
    methods: {
      ...mapActions([POST_SEND, POSTS_LOAD, POST_DELETE, USER_AVATAR_LOAD]),
      calendarLng() {
        if (this.currentLng === 'ru') {
          return ru;
        } else if (this.currentLng === 'en') {
          return en;
        }
      },
      dateFormatter(date) {
        return moment(date).format('MM-DD-YYYY');
      },
      clearGooglePlace(evn) {
        if(evn.target.attributes.name.nodeValue === 'autocompleteFilters') {
          if(!evn.target.value.length) this.filters.googlePlace = null
        } else {
          if(!evn.target.value.length) this.googlePlace = null
        }
      },
      selectType() {
        this.filters.categoriesArtistSeleted = null
        this.filters.skillsArtistSeleted = null
      },
      selectCategories() {
        this.filters.skillsArtistSeleted = null
      },
      removeCategories(removedOption) {
        if(Array.isArray(removedOption)) {
          removedOption.skills.forEach(s=>{
            let indexSkill = this.filters.skillsArtistSeleted.findIndex(sl=>sl.code===s)
            if(indexSkill>-1) this.filters.skillsArtistSeleted.splice(indexSkill,1)
          })
        } else {
          this.filters.skillsArtistSeleted = null
        }
      },
      removeType(removedOption) {
        if(Array.isArray(removedOption)) {
          removedOption.categories.forEach(t=>{
            let indexCat = this.filters.categoriesArtistSeleted.findIndex(c=>c.name_en===t.name_en)
            if(indexCat>-1) {
              this.filters.categoriesArtistSeleted[indexCat].skills.forEach(s=>{
                let indexSkill = this.filters.skillsArtistSeleted.findIndex(sl=>sl.code===s)
                if(indexSkill>-1) this.filters.skillsArtistSeleted.splice(indexSkill,1)
              })
              this.filters.categoriesArtistSeleted.splice(indexCat,1)
            }
          })
        } else {
          this.filters.categoriesArtistSeleted = null
          this.filters.skillsArtistSeleted = null
        }
      },
      typesLabel(option) {
        return option[`type_${this.currentLng}`]
      },
      categoriesLabel(option) {
        return option[`name_${this.currentLng}`]
      },
      skillsLabel(option) {
        return option[`skill_${this.currentLng}`]
      },
      loadPosts() {
        let whereParams = {
          and: []
        }
        if(!~this.filters.typeSeleted.indexOf('artist'))
          whereParams.and.push({participantType: {neq: 1}})
        if(!~this.filters.typeSeleted.indexOf('agent')) {
          whereParams.and.push({participantType: {neq: 2}})
        } else {
          if(this.filters.googlePlace)
            whereParams.and.push({'googlePlace.formatted_address': {eq: this.filters.googlePlace.formatted_address}})
        }
        if(Array.isArray(this.filters.typesArtistSeleted)) {
          // Работает только частично, нужно переделывать если потребуется
          this.filters.typesArtistSeleted.forEach(item=>whereParams.and.push({'artistTypesList.artistTypeId': {eq: item.id}}))
          this.filters.categoriesArtistSeleted.forEach(item=>whereParams.and.push({'artistTypesList.categories': {eq: item.name_en}}))
          this.filters.skillsArtistSeleted.forEach(item=>whereParams.and.push({'artistTypesList.skill': {eq: item.code}}))
        } else {
          if(this.filters.typesArtistSeleted) whereParams.and.push({'artistTypesList.artistTypeId': {eq: this.filters.typesArtistSeleted.id}})
          if(this.filters.categoriesArtistSeleted) whereParams.and.push({'artistTypesList.categories': {eq: this.filters.categoriesArtistSeleted.name_en}})
          if(this.filters.skillsArtistSeleted) whereParams.and.push({'artistTypesList.skill': {eq: this.filters.skillsArtistSeleted.code}})
        }
        if(!whereParams.and.length) delete whereParams.and
        this.POSTS_LOAD(this.getUserProfile.id,whereParams)
          .then(res=>{
            this.myPostsCount=res.myPostsCount
            this.posts=res.posts
            this.posts.forEach(element => {
              if(element.users) {
                if(!~this.cacheAvatars.findIndex(c=>c.id===element.usersId)) {
                  this.cacheAvatars.push({id: element.usersId})
                }
              } else {
                element.users = {}
                element.users.firstName = 'Unknow'
                element.users.lastName = 'User'
              }
            })
            this.cacheAvatars.forEach(c => {
              if(!c.avatar) {
                this.USER_AVATAR_LOAD(c)
                  .then((img)=>{
                    c.avatar = img
                    this.posts.filter(p=>p.usersId===c.id).forEach(p1=>p1.users.avatar=c.avatar)
                    this.posts.push({})
                    this.posts.splice(this.posts.length-1,1)
                  })
                  .catch((err)=>console.log(err))
              } else {
                this.posts.filter(p=>p.usersId===c.id).forEach(p1=>p1.users.avatar=c.avatar)
                this.posts.push({})
                this.posts.splice(this.posts.length-1,1)
              }
            })
          })
          .catch(err=>console.log(err))
      },
      clearPostFields() {
        this.post = {}
        this.selectedSkills = []
        this.text = ''
        this.date_start = null
        this.date_end = null
        this.googlePlace = null
        if(!this.isLookingJob) {
          this.$refs.autocomplete.$refs.input.value = ''
        }
      },
      setShowCollapse() {
        if(this.showCollapse) {
          this.clearPostFields()
        }
        this.showCollapse = !this.showCollapse
      },
      setPlace(place) {
        this.googlePlace = place
      },
      setPlaceFilters(place) {
        this.filters.googlePlace = place
      },
      deletePost(post,index) {
        swal({
          title: 'Are you sure?',
          text: "You won't be delete this post!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
          if (result.value) {
            this.POST_DELETE(post.id)
              .then(()=>{
                this.posts.splice(index,1)
                this.myPostsCount--
                swal(
                  'Deleted!',
                  'Your file has been deleted.',
                  'success'
                )
              })
              .catch(err=>console.log(err))
          }
        })
      },
      editPost(post) {
        this.post = post
        this.isLookingJob = post.participantType === 1
        this.text = post.localeTextsList[0].text
        this.selectedSkills = post.artistTypesList
        this.date_start = post.date_start ? (new Date(post.date_start)).toISOString().split('T')[0] : null
        this.date_end = post.date_end ? (new Date(post.date_end)).toISOString().split('T')[0] : null
        this.showCollapse = true

        this.$scrollTo('#elementEditPost', 1000, { container: '#scroll' })
      },
      test() {
        // this.isLookingJob = !this.isLookingJob
        // this.post.id = '000000011111'
        // console.log(this.cacheAvatars)
        console.log(this.myPostsCount)
        // return post.id===this.getUserProfile.id
        // this.$refs.autocomplete.innerText = null
        // this.$refs.autocomplete.$autocomplete = null
        // this.$refs.autocomplete.$refs.input.value = ''
        // setTimeout(() => {
        //   this.$refs.autocomplete.val('')
        //   this.$refs.autocomplete.focus()
        // },10)  
      },
      onlyDate(date) {
        let buf = new Date(date)
        return buf.toLocaleString('ru',{day: 'numeric', month: 'numeric', year: 'numeric'})
      },
      sendPost() {
        this.POST_SEND(this.postSend)
          .then(()=>{
            if(this.post.id) {
              let index = this.posts.findIndex(p=>p.id===this.post.id)
              this.$scrollTo('#post'+index, 1000, { container: '#scroll' })
            }
            this.showCollapse = false
            this.clearPostFields()
            this.loadPosts()
          })
          .catch((err)=>console.log(err))
      },
      getSkillString(skill) {
        let aType = this.artistTypes.find(t=>t.id===skill.artistTypeId)
        if(aType) {
          let aCat = aType.categories.find(c=>c.name_en===skill.categories)
          if(aCat) {
            let aSkill = this.skills.find(s=>s.code===skill.skill)
            if(aSkill) {
              return `${aType['type_'+this.currentLng]} -> ${aCat['name_'+this.currentLng]} -> ${aSkill['skill_'+this.currentLng]}`
            } else {
              console.log(`Ненайден skill артиста с code=${skill.skill}`)
            }
          } else {
            console.log(`Ненайдена категория артиста с ID=${skill.artistTypeId} name_en=${skill.categories}`)
          }
        } else {
          console.log(`Ненайден тип артиста с ID=${skill.artistTypeId}`)
        }
        return 'Error!'
      },
    },
    components: { SideBarPage, SpinnerLoader, CircleButton, SelectSkills, Multiselect, datepicker },
  }
</script>

