    cursor: not-allowed !important;
  }
  .checkbox-label {
    display: block
  }
  @media only screen and (max-width: 576px) {
    .colReverse { 
      -webkit-flex-direction: column-reverse;
      flex-direction: column-reverse;
    }  
  }
</style>

