import {
  GET_SKILLS,
  GET_ARTIST_TYPES,
  SET_SKILLS,
  SET_ARTIST_TYPES,
  GET_JOB_ADS,
  JOB_ADS_REQUEST, JOB_ADS_SUCCESS, JOB_ADS_ERROR
} from "../actions/skillsConst";
import axios from 'axios'

const state = {
  skillsApi: (process.env.VUE_APP_USTAGE_API || 'http://localhost:3000/api')+'/Skills',
  artistTypesApi: (process.env.VUE_APP_USTAGE_API || 'http://localhost:3000/api')+'/ArtistTypes',
  // jobAdsApi: process.env.VUE_APP_JOB_ADS_API || 'https://universtage.com/rest/searchAdvert/getAdsBySkill',
  jobAdsApi: process.env.VUE_APP_JOB_ADS_API || 'https://universtage.com/rest/searchAdvert/getAndSendAdsBySkill',
  skills: [],
  artistTypes: [],
  statusJobAds: '',
}

const getters = {
  isLoadingJobAds: state => state.statusJobAds==='loading',
}

const actions = {
  [GET_SKILLS]: ({commit}) => {
    return new Promise((resolve, reject) => {
      if(state.skills.length) {
        resolve(state.skills)
      } else {
        axios.get(state.skillsApi)
          .then(res=>res.data)
          .then(res=>{
            commit(SET_SKILLS, res)
            resolve(res)
          })
          .catch(err=>reject(err))
      }
    })
  },
  [GET_ARTIST_TYPES]: ({commit}) => {
    return new Promise((resolve, reject) => {
      if(state.artistTypes.length) {
        resolve(state.artistTypes)
      } else {
        axios.get(state.artistTypesApi)
          .then(res=>res.data)
          .then(res=>{
            commit(SET_ARTIST_TYPES, res)
            resolve(res)
          })
          .catch(err=>reject(err))
      }
    })
  },
  [GET_JOB_ADS]: ({commit}, query) => {
    return new Promise((resolve, reject) => {
      commit(JOB_ADS_REQUEST)
      axios.post(state.jobAdsApi, query)
        .then(res=>res.data)
        .then(res=>{
          commit(JOB_ADS_SUCCESS)
          resolve(res)
        })
        .catch(err=>{
          commit(JOB_ADS_ERROR)
          reject(err)
        })
    })
  },
}

const mutations = {
  [JOB_ADS_REQUEST]: (state) => {
    state.statusJobAds = 'loading'
  },
  [JOB_ADS_SUCCESS]: (state) => {
    state.statusJobAds = 'success'
  },
  [JOB_ADS_ERROR]: (state) => {
    state.statusJobAds = 'error'
  },
  [SET_SKILLS]: (state, skills) => {
    state.skills = skills
  },
  [SET_ARTIST_TYPES]: (state, types) => {
    state.artistTypes = types
  },
}

export default {
  state,
  getters,
  actions,
  mutations,
}
