<template>
    <side-bar-page>

    <template slot="content">
        <div class="content content-m">
            <div class="content-fluid">
                <div>
                    <b-modal v-model="showMessageModal"
                            :title="$t('pr_m_send_msg_header') + user.firstName + ' ' + user.lastName"
                            :body-text-variant="'dark'"
                            :footer-text-variant="'dark'"
                            :headerClass="'message-header'"
                            :modalClass="'message-modal'"
                            :footerClass="'message-footer'"
                            :hide-footer="true"
                            >
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12 disable-col-padding">
                                    <div class="form-group">
                                        <input type="text" class="form-control input-message" :placeholder="$t('pr_m_send_msg_placeholder')">
                                    </div>
                                   <div class="form-group">
                                        <textarea class="form-control text-message" placeholder="..."></textarea>
                                    </div>
                                    <button class="btn btn-fill float-right btn-primary btn-rounded" @click="sendMessage()">
                                        {{$t('pr_m_send_msg_btn')}}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </b-modal>
                </div>
                
                <div>
                    <b-modal v-model="showUploadImageModal"
                            :title="$t('pr_m_add_images')"
                            :body-text-variant="'dark'"
                            :footer-text-variant="'dark'"
                            :headerClass="'message-header'"
                            :modalClass="'message-modal'"
                            :footerClass="'message-footer'"
                            :hide-footer="true"
                            >
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12 disable-col-padding">
                                    <p>{{$t('pr_m_image_upload_txt')}}</p>
                                    
                                    <div class="col-md-12" :hidden="cropGalleryImg.loading">
                                        <croppa 
                                            v-model="cropGalleryImg"
                                            :width="1920"
                                            :height="1080"
                                            :quality="1"
                                            accept="image/*"
                                            :file-size-limit="35 * 1024 * 1024"
                                            :show-loading="false"
                                            @file-type-mismatch="onFileTypeMismatch"
                                            @file-size-exceed="onFileSizeExceed"
                                            @file-choose="coverLoaded"
                                            initial-size="cover"
                                            initial-position="top"
                                            hidden></croppa>
                                        <div class="row">
                                            <div class="col-md-12 disable-col-padding">
                                                <div class="text-left">
                                                    <span class="cover-text" v-html="galleryFiles"></span>
                                                    <button class="btn btn-default select-btn" @click="cropGalleryImg.chooseFile()">
                                                        <i class="fa fa-folder-open-o"></i> 
                                                        {{$t('pr_customize_select')}}
                                                    </button>
                                                </div>
                                            </div>  
                                        </div>
                                        <div class="row">
                                            <div class="col-md-12 disable-col-padding">
                                                <label class="sr-only" for="inlineFormInputGroup">Title</label>
                                                <div class="input-group mb-2">
                                                    <div class="input-group-prepend">
                                                    <div class="input-group-text"><i class="fa fa-picture-o" aria-hidden="true"></i></div>
                                                    </div>
                                                    <input type="text" v-model="uploadedImgTitle" :placeholder="$t('pr_m_placeholder_title')" class="form-control valid" maxlength="50">
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <button v-if="!isUploadingImgGallery" class="btn btn-fill float-right btn-primary btn-rounded btn-upload-gallery" @click="uploadCroppedImg(0.7)">
                                        {{$t('pr_m_upload_img')}}
                                    </button>
                                    <sync-loader v-if="isUploadingImgGallery" class="text-center" color="blue"></sync-loader>
                                </div>
                            </div>
                        </div>
                    </b-modal>
                </div>

                <div>
                    <b-modal v-model="showUploadVideoModal"
                            :title="$t('pr_m_add_videos')"
                            :body-text-variant="'dark'"
                            :footer-text-variant="'dark'"
                            :headerClass="'message-header'"
                            :modalClass="'message-modal'"
                            :footerClass="'message-footer'"
                            :hide-footer="true"
                            >
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12 disable-col-padding">
                                    <form action="javascript:void(0);" v-on:submit="postGalleryVideo()">
                                    <p>{{$t('pr_m_video_upload_txt')}}</p>
                                    <div class="row">
                                        <div class="col-md-6">
                                            <div class="form-group has-label">
                                                <input type="radio" name="videoRadios" id="youtubeRadio" value="youtube" v-model="video.media"/>
                                                <label class="form-radio" for="youtubeRadio"><span>{{$t('pr_m_youtube')}}</span></label>
                                            </div>
                                        </div>
                                        <div class="col-md-6">
                                            <div class="form-group has-label">
                                                <input type="radio" name="videoRadios" id="vimeoRadio" value="vimeo" v-model="video.media"/>
                                                <label class="form-radio" for="vimeoRadio"><span>{{$t('pr_m_vimeo')}}</span></label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-md-12 disable-col-padding">
                                        <label class="sr-only" for="inlineFormInputGroup">Title</label>
                                        <div class="input-group mb-2">
                                            <div class="input-group-prepend">
                                            <div class="input-group-text"><i class="fa fa-picture-o" aria-hidden="true"></i></div>
                                            </div>
                                            <input type="text" v-model="video.title" :placeholder="$t('pr_m_placeholder_title_video')" class="form-control valid" maxlength="50">
                                        </div>
                                    </div>
                                    <div class="col-md-12 disable-col-padding">
                                        <label class="sr-only" for="inlineFormInputGroup">Title</label>
                                        <div class="input-group mb-2">
                                            <div class="input-group-prepend">
                                            <div class="input-group-text"><i class="fa fa-link" aria-hidden="true"></i></div>
                                            </div>
                                            <input type="text" v-model="video.href" :placeholder="$t('pr_m_placeholder_link_video')" class="form-control valid" maxlength="100" required>
                                        </div>
                                    </div>
                                    <button type="submit" v-if="!isUploadingVideoGallery" class="btn btn-fill float-right btn-primary btn-rounded btn-upload-gallery">
                                        {{$t('pr_m_upload_video')}}
                                    </button>
                                    <sync-loader v-if="isUploadingVideoGallery" class="text-center" color="blue"></sync-loader>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </b-modal>
                </div>

                <div>
                    <b-modal v-model="showDeleteVideoModal"
                            :title="$t('pr_m_del_videos')"
                            :body-text-variant="'dark'"
                            :footer-text-variant="'dark'"
                            :headerClass="'message-header'"
                            :modalClass="'manage-gallery-modal'"
                            :footerClass="'message-footer'"
                            :hide-footer="true"
                            >
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12 disable-col-padding">
                                    <form action="javascript:void(0);">
                                    <div class="row">
                                        <div class="col-md-4 manage-col" v-for="(video, index) in videos" :key="index">
                                            <div class="video-container">
                                                <div class="delete-file-btn" @click="deleteVideoGallery(index)"><i class="fa fa-times-circle"></i></div>
                                                <img class="manage-gallery-video" :src="video.poster">
                                                <div class="overlay-manage">
                                                    <div class="overlay-text-manage">{{video.title}}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    </form>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-md-12">
                                    <sync-loader v-if="isDeletingVideoGallery" class="text-center" color="blue"></sync-loader>
                                </div>
                            </div>
                        </div>
                    </b-modal>
                </div>

                <div>
                    <b-modal v-model="showDeleteImageModal"
                            :title="$t('pr_m_del_images')"
                            :body-text-variant="'dark'"
                            :footer-text-variant="'dark'"
                            :headerClass="'message-header'"
                            :modalClass="'manage-gallery-modal'"
                            :footerClass="'message-footer'"
                            :hide-footer="true"
                            >
                        <div class="container-fluid">
                            <div class="row">
                                <div class="col-md-12 disable-col-padding">
                                    <form action="javascript:void(0);">
                                    <div class="row">
                                        <div class="col-md-4 manage-col" v-for="(image, index) in images" :key="index">
                                            <div class="video-container">
                                                <div class="delete-file-btn" @click="deleteImageGallery(index)"><i class="fa fa-times-circle"></i></div>
                                                <img class="manage-gallery-video" :src="image.href">
                                                <div class="overlay-manage">
                                                    <div class="overlay-text-manage">{{image.title}}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    </form>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-md-12">
                                    <sync-loader v-if="isDeletingImageGallery" class="text-center" color="blue"></sync-loader>
                                </div>
                            </div>
                        </div>
                    </b-modal>
                </div>

                
                <div class="row">
                    <div class="col-md-12">
                        <div class="card">
                            <div class="card-cover">
                                <div class="cover-img"  v-bind:style="{ 'background-image': 'url(' + coverPhoto + ')' }"></div>
                            </div>
                            <div class="card-body">
                                <div class="row">
                                    <div class="col-md-3">
                                        <div class="user">
                                            <img :src="profilePhoto" alt="..." class="avatar border-gray">
                                        </div>
                                    </div>
                                    <div class="col-md-9">
                                        <div class="user-title">
                                            <div class="title">
                                            <span :hidden="user.userType == 'team'">{{user.firstName}} {{user.lastName}}</span>
                                            <span :hidden="(user.userType == 'artist') || (user.userType == 'agent')">{{user.teamName}}</span>
                                            <span class="hidden-xs social-buttons-block" style="display:none !important;">
                                                <button class="btn btn-info btn-fill btn-wd btn-icon" @click="writeMessage()">
                                                    <i class="fa fa-pencil-square-o"></i> <span class="btn-txt">{{$t('pr_m_msg_btn')}}</span>
                                                </button>
                                                <button class="btn btn-info btn-fill btn-wd btn-icon">
                                                    <i class="fa fa-plus"></i> <span class="btn-txt">{{$t('pr_m_follow_btn')}}</span>
                                                </button>
                                            </span>
                                        
                                            </div>
                                            <small v-if="user.userType == 'team'">{{$t('pr_m_circus_team')}}</small>
                                            <small v-if="user.userType == 'artist'">{{$t('pr_m_artist')}}</small>
                                            <small v-if="user.userType == 'agent'">{{$t('pr_m_agent')}}</small>
                                            <div class="hidden-md visible-xs" style="display:none !important;">
                                                <div class="btn-mobile-block">
                                                    <button class="btn btn-info btn-fill btn-wd btn-icon" @click="writeMessage()">
                                                        <i class="fa fa-pencil-square-o"></i> <span class="btn-txt">{{$t('pr_m_msg_btn')}}</span>
                                                    </button>
                                                    <button class="btn btn-info btn-fill btn-wd btn-icon">
                                                        <i class="fa fa-plus"></i> <span class="btn-txt">{{$t('pr_m_follow_btn')}}</span>
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-2"></div>
                                    <div class="col-md-8">
                                        <p class="description text-center">
                                            <span>{{user.aboutMe}}</span>
                                        </p>
                                    </div>
                                    <div class="col-md-2"></div>
                                </div>
                                <div class="row">
                                    <div class="col-md-5 offset-md-1 col-mobile-margin">
                                        <div class="base-info-txt">
                                            <p class="base-info-header">{{$t('pr_m_about_me')}}</p>
                                            <p :hidden="user.userType == 'agent'"><i class="fa fa-phone fa-info-block" aria-hidden="true"></i> {{$t('pr_m_phone')}}: <a class="data" :href="'tel:'+user.phone1">{{user.phone1 || $t('pr_not_defined')}}</a></p>
                                            <p :hidden="user.userType == 'agent'"><i class="fa fa-envelope fa-info-block" aria-hidden="true"></i> {{$t('pr_m_email')}}: <a class="data" :href="'mailto:'+user.email">{{user.email || $t('pr_not_defined')}}</a></p>
                                            <p><i class="fa fa-flag fa-info-block" aria-hidden="true"></i> {{$t('pr_m_country')}}: <span class="data">{{user.country || $t('pr_not_defined')}}</span></p>
                                            <p><i class="fa fa-globe fa-info-block" aria-hidden="true"></i> {{$t('pr_m_city')}}: <span class="data">{{user.city || $t('pr_not_defined')}}</span></p>
                                        </div>
                                    </div>
                                    <div class="col-md-5 offset-md-1" v-for="(artist, index) in range(0, 1, user.artistTypesList)" :value="artist" :key="index" :hidden="user.userType == 'agent'">
                                        <div class="base-info-txt">
                                            <p class="base-info-header">{{$t('pr_m_about_skills')}}</p>
                                            <p><i class="fa fa-id-badge fa-info-block" aria-hidden="true"></i> {{$t('pr_m_artist_type')}}: <span class="data">{{getArtistType(artist.artistTypeId)['type_'+currentLng]}}</span></p>
                                            <p><i class="fa fa-key fa-info-block" aria-hidden="true"></i> {{$t('pr_m_categories')}}: <span class="data">{{getCategory(artist.artistTypeId, artist.categories)['name_'+currentLng]}}</span></p>
                                            <p><i class="fa fa-trophy fa-info-block" aria-hidden="true"></i> {{$t('pr_m_skills')}}: <span class="data">{{getSkill(artist.skill)['skill_'+currentLng]}}</span></p>
                                            <p><span class="data more-info-btn" v-b-toggle="'collapseSkills'" @click="collapseSkills()">
                                                    <span v-if="isCollapse">{{$t('pr_m_less_info')}}</span> 
                                                    <span v-if="!isCollapse">{{$t('pr_m_more_info')}}</span>
                                               </span>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <b-collapse id="collapseSkills">
                                    <div class="row collapseSkills">
                                        <div class="col-md-5 offset-md-1">
                                            <div class="base-info-txt">
                                                <p class="base-info-header additionalSkills">{{$t('pr_m_all_skiils')}}</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="row">
                                        <div class="col-md-5 offset-md-1 artistSkills" v-for="(artist, index) in range(1, user.artistTypesList.length, user.artistTypesList)" :value="artist" :key="index">
                                            <div class="base-info-txt">
                                                <p><i class="fa fa-id-badge fa-info-block" aria-hidden="true"></i> {{$t('pr_m_artist_type')}}: <span class="data">{{getArtistType(artist.artistTypeId)['type_'+currentLng]}}</span></p>
                                                <p><i class="fa fa-key fa-info-block" aria-hidden="true"></i> {{$t('pr_m_categories')}}: <span class="data">{{getCategory(artist.artistTypeId, artist.categories)['name_'+currentLng]}}</span></p>
                                                <p><i class="fa fa-trophy fa-info-block" aria-hidden="true"></i> {{$t('pr_m_skills')}}: <span class="data">{{getSkill(artist.skill)['skill_'+currentLng]}}</span></p>
                                            </div>
                                        </div>
                                    </div>
                                </b-collapse>
                                <div class="row row-gallery-title">
                                    <div class="col-md-12 disable-col-padding">
                                        <span class="image-gallery-label">{{$t('pr_m_photo')}}</span>
                                        <span class="image-gallery-label" v-if="isLogin">
                                            <span class="add-image-btn" @click="uploadImages()">
                                                |<i class="fa fa-plus"></i>{{$t('pr_m_photo_add')}}
                                            </span>
                                        </span>
                                        <span class="image-gallery-label" v-if="isLogin">
                                            <span class="add-image-btn" @click="deleteImages()">
                                                |<i class="fa fa-share-alt"></i>{{$t('pr_m_photo_manage')}}
                                            </span>
                                        </span>
                                    </div>
                                </div>
                                <div class="row row-gallery">
                                    <div class="col-md-12 disable-col-padding">
                                        <gallery :images="images" :index="index" @close="index = null"></gallery>
                                        <div class="image gallery-image" v-for="(image, imageIndex) in images" :key="imageIndex"
                                            @click="index = imageIndex" 
                                            :style="{ backgroundImage: 'url(' + image.href + ')'}">
                                            <div class="overlay">
                                                <div class="overlay-text">{{image.title}}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-12 disable-col-padding">
                                        <span class="image-gallery-label">{{$t('pr_m_video')}}</span>
                                        <span class="image-gallery-label" v-if="isLogin">
                                            <span class="add-video-btn" @click="uploadVideos()">
                                                |<i class="fa fa-plus"></i>{{$t('pr_m_video_add')}}
                                            </span>
                                        </span>
                                        <span class="image-gallery-label" v-if="isLogin">
                                            <span class="add-video-btn" @click="deleteVideos()">
                                                |<i class="fa fa-share-alt"></i>{{$t('pr_m_video_manage')}}
                                            </span>
                                        </span>
                                    </div>
                                </div>
                                <div class="row row-gallery-video">
                                    <div class="col-md-12 disable-col-padding">
                                        <gallery 
                                            :images="videos" 
                                            :index="indexVideo" 
                                            :options="{youTubeVideoIdProperty: 'youtube', youTubePlayerVars: undefined, youTubeClickToPlay: true}"
                                            @close="indexVideo = null"
                                        ></gallery>
                                        <div class="video gallery-image"
                                            v-for="(video, videoIndex) in videos" :key="videoIndex"
                                            @click="indexVideo = videoIndex"
                                            :style="{ backgroundImage: 'url(' + video.poster + ')'}">
                                            <div class="overlay">
                                                <div class="overlay-text">{{video.title}}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </template>

    </side-bar-page>
</template>


<script>
  import SideBarPage from '../components/SideBarPage';
  import { mapActions, mapGetters} from 'vuex';
  import { USER_GALLERY_IMG_SEND, USER_GALLERY_VIDEO_SEND, USER_GALLERY_VIDEO_LOAD, USER_GALLERY_IMAGE_LOAD, 
            USER_AVATAR_LOAD, USER_COVER_LOAD, USER_PROFILE_LOAD, IMAGE_DELETE, VIDEO_DELETE } from "../store/actions/userConst";
  import { GET_ARTIST_TYPES, GET_SKILLS } from "../store/actions/skillsConst";
  import syncLoader from 'vue-spinner/src/PulseLoader.vue';
  import swal from 'sweetalert2'
  import gallery from 'vue-gallery';
  import axios from 'axios';

  export default {
    components: { SideBarPage, syncLoader, gallery },
    created() {
        this.GET_ARTIST_TYPES()
            .then(res=> {
                this.artistTypes=res;
            }).catch(err=>console.log(err));
        this.GET_SKILLS()
            .then(res=> {
                this.skills=res;
            }).catch(err=>console.log(err));
        this.USER_GALLERY_VIDEO_LOAD({
            id: this.$route.params.id
        }).then(res=> {
            if (res.successful) {
                this.videos = res.data;
                console.log('videos');
                console.log(this.videos);
            } else {
                this.videos = [];
            }
        }).catch(err=>console.log(err));
        this.USER_GALLERY_IMAGE_LOAD({
            id: this.$route.params.id
        }).then(res=> {
            if (res.successful) {
                this.images = res.data;
                console.log('images');
                console.log(this.images);
            } else {
                this.images = [];
            }
        }).catch(err=>console.log(err));
        this.USER_AVATAR_LOAD({
            id: this.$route.params.id
        }).then(res=> {
            this.profilePhoto = res;
        }).catch(err=>console.log(err));
        this.USER_COVER_LOAD({
            id: this.$route.params.id
        }).then(res=> {
            this.coverPhoto = res;
        }).catch(err=>console.log(err));
        this.USER_PROFILE_LOAD({
            id: this.$route.params.id
        }).then(res=> {
            this.user = res;
            console.log(res);
        }).catch(err=>console.log(err));
    },
    data(){
      return {
        showMessageModal: false,
        showUploadImageModal: false,
        showUploadVideoModal: false,
        showDeleteVideoModal: false,
        showDeleteImageModal: false,
        isCollapse: false,
        skills: {},
        artistTypes: {},
        profilePhoto: {},
        coverPhoto: {},
        cropGalleryImg: {},
        uploadedImgTitle: '',
        galleryFiles: this.$t('pr_no_file'),
        video: {
            title: '',
            href: '',
            poster: '',
            youtube: '',
            media: 'youtube'
        },
        user: {
            firstName: '',
            lastName: '',
            city: '',
            country: '',
            phone1: '',
            email: '',
            nickName: '',
            teamName: '',
            numberOfPersons: '',
            aboutMe: '',
            accountType: '',
            artistTypesList: []
        },
        images: [],
        videos: [],
        index: null,
        indexVideo: null,
        jsonVimeo: null
      }
    },
    computed: {
        ...mapGetters(['currentLng', 'isLogin', 'isUploadingImgGallery', 'isUploadingVideoGallery', 'isDeletingVideoGallery', 'isDeletingImageGallery']),
    },
    methods: {
        ...mapActions([GET_SKILLS, GET_ARTIST_TYPES, USER_GALLERY_IMG_SEND, USER_GALLERY_VIDEO_SEND, USER_GALLERY_VIDEO_LOAD, 
                        USER_GALLERY_IMAGE_LOAD, USER_AVATAR_LOAD, USER_COVER_LOAD, USER_PROFILE_LOAD, IMAGE_DELETE, VIDEO_DELETE]),
        writeMessage() {
            this.showMessageModal = !this.showMessageModal;
        },
        uploadImages() {
            this.showUploadImageModal = !this.showUploadImageModal;
        },
        uploadVideos() {
            this.showUploadVideoModal = !this.showUploadVideoModal;
        },
        deleteVideos() {
            this.showDeleteVideoModal = !this.showDeleteVideoModal;
        },
        deleteImages() {
            this.showDeleteImageModal = !this.showDeleteImageModal;
        },
        sendMessage() {
            // place code for send message here
            this.showMessageModal = false;
        },
        collapseSkills() {
            this.isCollapse = !this.isCollapse;
        },
        range(start, end, arr) {
            if(typeof arr !== 'undefined') {
                return arr.slice(start, end);
            } else {
                return false;
            }
        },
        getArtistType(artistId) {
            let result = this.artistTypes.find(t => t.id === artistId);
            if ((typeof result !== 'undefined') && result) {
                return result;
            } else {
                return 'error';
            }
        },
        getCategory(artistId, categoryId) {
            let artistType = this.artistTypes.find(t => t.id === artistId);
            let category = artistType.categories.find(a => a.name_en === categoryId);
            if ((typeof category !== 'undefined') && category) {
                return category;
            } else {
                return 'error';
            }
        },
        getSkill(skillId) {
            let result = this.skills.find(s => s.code === skillId);
            if ((typeof result !== 'undefined') && result) {
                return result;
            } else {
                return 'error';
            }
        },
        onFileTypeMismatch () {
            this.galleryFiles = this.$t('pr_no_file');
            const Toast = swal.mixin({
                toast: true,
                position: 'bottom-start',
                customClass: 'sweet-modal',
                showConfirmButton: false,
                timer: 3000
            });
            Toast.fire({
            title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_invalid_file')
            });
        },
        onFileSizeExceed () {
            this.galleryFiles = this.$t('pr_no_file');
            const Toast = swal.mixin({
                toast: true,
                position: 'bottom-start',
                customClass: 'sweet-modal',
                showConfirmButton: false,
                timer: 3000
            });
            Toast.fire({
            title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_big_file')
            });
        },
        coverLoaded() {
            this.galleryFiles = this.cropGalleryImg.$el.firstElementChild.files[0].name;
            if(this.galleryFiles.length > 20) {
                this.galleryFiles = this.galleryFiles.substring(0, 18)+"..";
            }
        },
        uploadCroppedImg(compressionRate) {
            this.cropGalleryImg.generateBlob((blob) => {
            blob.lastModified = new Date();
            blob.name = 'my-image.jpg';
            blob.__proto__ = File.prototype;
            this.blobImageCover = blob;
            this.postGalleryImage(blob.name, blob);
            }, 'image/jpeg', compressionRate);
        },
        postGalleryImage(name, blob) {
            let formData = new FormData();
            formData.append('file', blob, name);
            this.USER_GALLERY_IMG_SEND({
                image: formData,
                ownerId: this.$route.params.id,
                dateCreating: new Date().toISOString(),
                media: 'image',
                title: this.uploadedImgTitle || ' '
            }).then((res)=>{
                console.log(res);
                this.USER_GALLERY_IMAGE_LOAD({
                    id: this.$route.params.id
                }).then(res=> {
                    if (res.successful) {
                        this.images = res.data;
                    } else {
                        this.images = [];
                    }
                }).catch(err=>console.log(err));
                const Toast = swal.mixin({
                    toast: true,
                    position: 'bottom-start',
                    customClass: 'sweet-modal',
                    showConfirmButton: false,
                    timer: 3000
                });
                Toast.fire({
                    title: '<i class="fa fa-check fa-2x"></i>' + this.$t('pr_m_gallery_uploaded')
                });
                this.showUploadImageModal = false;
                this.uploadedImgTitle = '';
                this.galleryFiles = this.$t('pr_no_file');
            })
            .catch((err)=> {
            console.log(err);
            const Toast = swal.mixin({
                toast: true,
                position: 'bottom-start',
                customClass: 'sweet-modal',
                showConfirmButton: false,
                timer: 3000
            });
            Toast.fire({
                title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_m_gallery_not_uploaded')
            });
            this.showUploadImageModal = false;
            });
        },
        postGalleryVideo() {
            // if youtube video
            if (this.video.media === 'youtube') {
                let index = this.video.href.indexOf('?');
                if (index !== -1) {
                    this.video.youtube = this.video.href.substring(index+3, this.video.href.length);
                    this.video.poster = 'https://img.youtube.com/vi/'+ this.video.youtube +'/maxresdefault.jpg'
                }   
                this.USER_GALLERY_VIDEO_SEND({
                    href: this.video.href,
                    ownerId: this.$route.params.id,
                    dateCreating: new Date().toISOString(),
                    media: this.video.media,
                    title: this.video.title || ' ',
                    poster: this.video.poster,
                    youtube: this.video.youtube
                }).then((res) => {
                    console.log(res);
                    this.USER_GALLERY_VIDEO_LOAD({
                        id: this.$route.params.id
                    }).then(res=> {
                        if (res.successful) {
                            this.videos = res.data;
                        } else {
                            this.videos = [];
                        }
                    }).catch(err=>console.log(err));
                    const Toast = swal.mixin({
                        toast: true,
                        position: 'bottom-start',
                        customClass: 'sweet-modal',
                        showConfirmButton: false,
                        timer: 3000
                    });
                    Toast.fire({
                        title: '<i class="fa fa-check fa-2x"></i>' + this.$t('pr_m_gallery_video_uploaded')
                    });
                    this.showUploadVideoModal = false;
                    this.video.title = '';
                    this.video.href = '';
                }).catch((err)=> {
                    console.log(err);
                    const Toast = swal.mixin({
                        toast: true,
                        position: 'bottom-start',
                        customClass: 'sweet-modal',
                        showConfirmButton: false,
                        timer: 3000
                    });
                    Toast.fire({
                        title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_m_gallery_video_not_uploaded')
                    });
                    this.showUploadVideoModal = false;
                });
            } else if (this.video.media === 'vimeo') {
                // if vimeo video
                let index = this.video.href.indexOf('m/');
                if (index !== -1) {
                    this.video.youtube = this.video.href.substring(index+2, this.video.href.length);
                    console.log(this.video.youtube);
                }
                axios.get('http://vimeo.com/api/v2/video/' + this.video.youtube + '.json?callback=showThumb')
                .then(response => {
                    this.jsonVimeo = response.data;
                    let firstIndex = this.jsonVimeo.indexOf('thumbnail_medium');
                    let secondIndex = this.jsonVimeo.indexOf('thumbnail_large');
                    if ((firstIndex !== -1) && (secondIndex !== -1)) {
                        var tmpStr = this.jsonVimeo.substring(firstIndex+19, secondIndex);
                        var thirdIndex = tmpStr.indexOf('video');
                        if(thirdIndex !== -1) {
                            var thumbnail = tmpStr.substring(thirdIndex+7,tmpStr.length-15);
                            this.video.poster = 'https://i.vimeocdn.com/video/' + thumbnail + '_640.jpg';
                        }
                    }

                    this.USER_GALLERY_VIDEO_SEND({
                        href: this.video.href,
                        ownerId: this.$route.params.id,
                        dateCreating: new Date().toISOString(),
                        media: this.video.media,
                        title: this.video.title || ' ',
                        poster: this.video.poster,
                        youtube: this.video.youtube
                    }).then((res) => {
                        console.log(res);
                        this.USER_GALLERY_VIDEO_LOAD({
                            id: this.$route.params.id
                        }).then(res=> {
                            if (res.successful) {
                               this.videos = res.data;
                            } else {
                                this.videos = [];
                            }
                        }).catch(err=>console.log(err));
                        const Toast = swal.mixin({
                            toast: true,
                            position: 'bottom-start',
                            customClass: 'sweet-modal',
                            showConfirmButton: false,
                            timer: 3000
                        });
                        Toast.fire({
                            title: '<i class="fa fa-check fa-2x"></i>' + this.$t('pr_m_gallery_video_uploaded')
                        });
                        this.showUploadVideoModal = false;
                    }).catch((err)=> {
                        console.log(err);
                        const Toast = swal.mixin({
                            toast: true,
                            position: 'bottom-start',
                            customClass: 'sweet-modal',
                            showConfirmButton: false,
                            timer: 3000
                        });
                        Toast.fire({
                            title: '<i class="fa fa-times fa-2x"></i>' + this.$t('pr_m_gallery_video_not_uploaded')
                        });
                        this.showUploadVideoModal = false;
                    });
                }).catch(error => console.log(error));
            }
        },
        deleteVideoGallery(index) {
            this.VIDEO_DELETE(this.videos[index]).then((res)=> {
              console.log(res);
              this.USER_GALLERY_VIDEO_LOAD({
                id: this.$route.params.id
              }).then(res=> {
                if (res.successful) {
                    this.videos = res.data;
                    console.log('videos');
                    console.log(this.videos);
                } else {
                    this.videos = [];
                }
              }).catch(err=>console.log(err));
            }).catch((err)=> {
              console.log(err);
            });    
        },
        deleteImageGallery(index) {
            this.IMAGE_DELETE(this.images[index]).then((res)=> {
              console.log(res);
              this.USER_GALLERY_IMAGE_LOAD({
                id: this.$route.params.id
              }).then(res=> {
                if (res.successful) {
                    this.images = res.data;
                    console.log('images');
                    console.log(this.images);
                } else {
                    this.images = [];
                }
              }).catch(err=>console.log(err));
            }).catch((err)=> {
              console.log(err);
            });    
        },
    }
  }
  </script>

